declare var ResizeSensor: any;
export var initCanvas = function() {
  //$("#canvas-area").append(gl.canvas);
  sc_w = gl.canvas.width = $("canvas").width();
  sc_h = gl.canvas.height = $("canvas").height();
  $("#progressbar").progress("set percent", 0);
  var button = document.createElement("div");
  button.className = "critique-tag";
  $(".critique-list").append(button);
  $(button).css("left", "87%");
  //glContextBuffer.resizeCanvas(sc_w, sc_h);
  $("#progressbar").mousemove(function(e: JQuery.Event<HTMLElement, null>) {
    var left = $(this).offset().left;
    var percent = (e.pageX - left) / $(this).width();
    //console.log(""+e.pageX+","+$(this).width())

    $(this).progress("set duration", 10);
    $(this).progress("set percent", percent * 100);
    //paintReplayer.commands = masterRecorder.commands;
    paintReplayer.drawToProgressPercent(percent);
  });
  resizeWindow();
  //  new ResizeSensor($("#canvas-area"), resizeWindow);
  function resizeWindow() {
    //glContextBuffer.resize(gl.canvas)
    sc_w = gl.canvas.width = 1280; //1024; //$("canvas").width();
    sc_h = gl.canvas.height = 1024; //$("canvas").height();
    //console.log(sc_w);
    console.log(sc_h);
    if (!is_web_browser) {
      var screenOffset = ipcRenderer.sendSync("getOffset", "offset");
    } else {
      screenOffset = [0, 0];
    }

    var offset = gl.canvas.getBoundingClientRect();
    mouseEventHandler.offsetx = -offset.left + screenOffset[0];
    mouseEventHandler.offsety = offset.top + screenOffset[1];
    console.log(offset.left);
    glContextBuffer.resizeCanvas(sc_w, sc_h);
    //glContextBuffer.renderCanvas()
  }
};

export var mouseEventHandler: MouseEventHandler;
export var glContextBuffer: GLContextBuffer;
export var textureManager: TextureManager;
export function loadPainter() {
  // gl = GL.create({
  //   antialias: false,
  //   preserveDrawingBuffer: true,
  //   premultipledAlpha: true
  // });

  var keybinder = new KeyBinder();

  var palatteColor = [1.0, 0.0, 0.0, 1.0];
  var currentColor: number[];
  //1240 1754
  var width = 1024;
  var height = 768;

  var layers = new Array();
  layers.push(new GL.Texture(width, height));
  var currentLayer = layers[0];

  gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, true);
  textureManager = new TextureManager();
  paintToolManager = new PaintToolManager();
  glContextBuffer = new GLContextBuffer(width, height);
  mouseEventHandler = new MouseEventHandler(glContextBuffer);

  var startPos;
  var force = 0;
  //gl.fullscreen({ fov: 45, near: 0.1, far: 1000 });

  var canvasScale = 1;
  var disx = 0,
    disy = 0;
  var pivotx = 0,
    pivoty = 0;
  var newScale = 1;

  // function readPixels() {
  //   var pixels = new Uint8Array(
  //     gl.drawingBufferWidth * gl.drawingBufferHeight * 4
  //   );
  //   gl.readPixels(
  //     0,
  //     0,
  //     gl.drawingBufferWidth,
  //     gl.drawingBufferHeight,
  //     gl.RGBA,
  //     gl.UNSIGNED_BYTE,
  //     pixels
  //   );
  //   console.log(pixels); // Uint8Array
  // }
  // function blendColorAlpha(c1: number[], c2: number[], weight: number) {
  //   var a1 = c1[3];
  //   var a2 = c2[3];
  //   var af = a1 + a2 * (1 - a1);
  //   var cf = new Array(4);
  //   for (var i = 0; i < 4; i++) {
  //     cf[i] = (c1[i] * a1 + c2[i] * a2 * (1 - a1)) / af;
  //   }
  //   cf[3] = af;
  //   return cf;
  // }

  //clearCanvas();
  var pos;
  var lastPos: GL.Vector;
  var dir;
  var brushType = "brush";
  var isMouseDown = false;
  var isPen = false;
  var plugin = document.getElementById("wtPlugin");
  var isMouse = true;
  var lastPoint: PointData;
  var scroll = 3;
  var canZoom = true;
  var lastPivotX = 0;
  var lastPivotY = 0;
  var oldCanvasPosX = 0;
  var oldCanvasPosY = 0;
  var canvasPos = new GL.Vector(0, 0, 0);
  var scaleTable = [0.2, 0.333, 0.5, 1, 2, 4, 5, 6, 8];
  var macScroll = 0;

  paintReplayer = new PaintReplayer(glContextBuffer);
  paintRecorder = new PaintRecorder();
  fileManager = new FileManager();
  paintToolManager.setDefaultTool();
  $("#load-btn").click(function() {
    //strokeRenderer.drawStrokes(paintRecorder.clip.commands);
    //renderScene();
    //paintReplayer.excuteDrawingCommands(masterRecorder.commands);
    paintReplayer.restartReplay();
  });
  //paintReplayer.clip = paintRecorder.clip;

  function replayAllStroke() {
    var i = 0;

    var timer = setInterval(function() {
      glContextBuffer.strokeRenderer.drawStrokes([
        paintRecorder.clip.fullCommands[i++]
      ]);
      if (i == paintRecorder.clip.fullCommands.length) {
        clearInterval(timer);
      }
      glContextBuffer.renderCanvas();
    }, 20);
  }

  $("#clear-btn").click(function() {
    glContextBuffer.clearCanvas();
    glContextBuffer.renderCanvas();
  });
  glContextBuffer.renderCanvas();
  $("input#opacity").change(function() {
    glContextBuffer.currentLayer.opacity = $(this).val() as number;
    glContextBuffer.renderCanvas();
  });
  $("input#layerenable").change(function() {
    glContextBuffer.currentLayer.isHidden = !glContextBuffer.currentLayer
      .isHidden;
    glContextBuffer.renderCanvas();
  });
  var count = 0;

  GLContextBuffer.isDirty = true;
  var isPlaying = true;
  function renderCanvas() {
    if (isPlaying) {
      requestAnimationFrame(renderCanvas);
      glContextBuffer.renderCanvas();
    }
  }
  renderCanvas();
}

export function dismissPainter() {}



