import * as React from "react";
import { Link, Router, RouteComponentProps, match } from "react-router-dom";
import {
  Menu,
  Button,
  Modal,
  Header,
  Image,
  Icon,
  Input,
  Grid,
  Popup
} from "semantic-ui-react";
import { observable } from "mobx";
import { observer } from "mobx-react";
import { DatabaseTableURL } from "../../engine/Firebase/DatabaseTableURL";
import { CreateMaterialPopUp } from "../Material/Create/CreateMaterialPopup";
import { firebaseManager } from "../index";
import { LoginModal } from "./LoginModal";

observable(document.location);

window.onpopstate = function(e) {
  console.log(
    "location: " + document.location + ", state: " + JSON.stringify(e.state)
  );
};

const UserStatus = () =>
  <Menu.Item>
    <Icon name="bell" />
    {firebaseManager.loginManager.user.displayName + ",你好 "}
    {<a onClick={() => firebaseManager.loginManager.logout()}>登出</a>}
  </Menu.Item>;

@observer
export class Nav extends React.Component<any, { activeItem: string }> {
  constructor() {
    super();
    this.state = { activeItem: "home" };
  }

  handleItemClick = (e, { name }) => this.setState({ activeItem: name });
  login() {}
  render() {
    return (
      <div style={{ margin: 10 }}>
        {/* <div className="ui menu">          
          <a
            className="item"
            onClick={function() {
              history.back();
            }}
          >
            <i className="icon Arrow Left" />{" "}
          </a>
          <a
            className="item"
            onClick={function() {
              history.forward();
            }}
          >
            <i className="icon Arrow Right" />{" "}
          </a> */}
        <CreateMaterialPopUp />
        <Menu inverted fixed="top">
          <Link to="/">
            <Menu.Item>SketchShare</Menu.Item>
          </Link>
          <Menu.Item style={{ width: "50%" }}>
            <Input fluid icon="search" placeholder="Search..." />
          </Menu.Item>

          {/* <Menu.Item>
            {console.log(document.location.pathname)}
            {document.location.hash}
          </Menu.Item> */}

          <Menu.Menu className="right">
            <Link to="/" onClick={function() {}}>
              <Menu.Item
                name="home"
                active={this.state.activeItem === "home"}
                onClick={this.handleItemClick}
              >
                首頁
              </Menu.Item>
            </Link>
            {/* <Link to={DatabaseTableURL.Material} onClick={function() {}}>
              <Menu.Item
                name="material_collections"
                active={this.state.activeItem === "material_collections"}
                onClick={this.handleItemClick}
              >
                探索素材
              </Menu.Item>
            </Link> */}
            <Link to={DatabaseTableURL.Me}>
              <Menu.Item
                name="dashboard"
                active={this.state.activeItem === "dashboard"}
                onClick={this.handleItemClick}
              >
                個人頁面
              </Menu.Item>
            </Link>
            <Popup
              trigger={<Menu.Item icon="bars" />}
              hoverable
              on="click"
              position="bottom center"
              content="Hide the popup on any scroll event"
            />
            <Popup
              trigger={<Button icon="add" />}
              content="Add users to your feed"
            />
            {firebaseManager.loginManager.user
              ? <UserStatus />
              : <LoginModal
                  info="登入"
                  triggerComponent={<Menu.Item>登入</Menu.Item>}
                />}
          </Menu.Menu>
        </Menu>
      </div>
    );
  }
}



