import { createMaterialStore } from "../CreateMaterialStore";
import glamorous from "glamorous";
import * as React from "react";
import Transition from "react-motion-ui-pack";
import {
  Segment,
  Rail,
  Card,
  CardGroup,
  Image,
  Button,
  Menu,
  Icon,
  Item
} from "semantic-ui-react";
import { observer } from "mobx-react";
import * as ReactDOM from "react-dom";
export const HoriScrollArea = glamorous.div({
  height: 164,
  whiteSpace: "nowrap",
  overflowX: "auto"
});
const WrapDiv = glamorous.div({
  display: "inline-block",
  position: "relative",
  height: "100%"
});
export class PreviewImage extends React.Component<
  { src: string; index: number; deletable?: boolean; OnClick?: Function },
  any
> {
  remove() {
    createMaterialStore.images.splice(this.props.index, 1);
  }

  render() {
    return (
      <WrapDiv key={this.props.index}>
        <img
          style={{ height: "100%" }}
          src={this.props.src}
          onClick={() => {
            this.props.OnClick();
          }}
        />
        {this.props.deletable
          ? <Button
              circular
              size="mini"
              icon="close"
              style={{
                position: "absolute",
                top: 5,
                right: 5,
                margin: 3
              }}
              onClick={() => this.remove()}
            />
          : null}
      </WrapDiv>
    );
  }
}
@observer
export class PreviewMaterialGrid extends React.Component<
  { deleteable?: boolean },
  any
> {
  scrollArea;
  render() {
    return (
      // <HoriScrollArea>
      //   <HoriCard image="http://a2.mzstatic.com/us/r30/Purple118/v4/48/e0/18/48e018d5-e7b6-a50b-04f4-88609705c905/screen520x924.jpeg" />
      //   <HoriCard image="http://a2.mzstatic.com/us/r30/Purple118/v4/48/e0/18/48e018d5-e7b6-a50b-04f4-88609705c905/screen520x924.jpeg" />
      //   <HoriCard image="http://a2.mzstatic.com/us/r30/Purple118/v4/48/e0/18/48e018d5-e7b6-a50b-04f4-88609705c905/screen520x924.jpeg" />
      //   <HoriCard image="http://a2.mzstatic.com/us/r30/Purple118/v4/48/e0/18/48e018d5-e7b6-a50b-04f4-88609705c905/screen520x924.jpeg" />
      // </HoriScrollArea>
      <div>
        <Transition
          component={false}
          enter={{
            height: 164,
            opacity: 1
          }}
          leave={{
            height: 0,
            opacity: 0
          }}
        >
          {createMaterialStore.images.length > 0 &&
            <div key="area">
              <HoriScrollArea
                ref={s => {
                  this.scrollArea = s;
                }}
                onWheel={e => {
                  e.preventDefault();
                  ReactDOM.findDOMNode(this.scrollArea).scrollLeft += e.deltaY;
                }}
              >
                {createMaterialStore.images.map((image, index) => {
                  console.log(index);
                  return (
                    <PreviewImage
                      src={image.data}
                      index={index}
                      deletable={this.props.deleteable}
                    />
                  );
                })}
              </HoriScrollArea>
            </div>}
        </Transition>
      </div>
    );
  }
}



