import * as React from "react";
import { Input, Button, Image, Grid } from "semantic-ui-react";
import { createMaterialStore, ImageRef } from "../CreateMaterialStore";
export class MaterialURLInput extends React.Component<
  any,
  { currentImageURL: string; imageValid: boolean }
> {
  componentWillMount() {
    this.state = { currentImageURL: "", imageValid: false };
    //this.createMaterial.bind(this);
  }
  parseNewImgURL(url: string) {
    console.log(url);
    this.setState({ currentImageURL: url, imageValid: false });
  }
  handleImgLoaded() {
    console.log("loaded");
    this.setState({ imageValid: true });
  }
  addValidImage() {
    createMaterialStore.addImage(
      new ImageRef("url", this.state.currentImageURL)
    );
  }
  render() {
    return (
      <span className="">
        <Input
          action
          fluid
          placeholder="貼上圖片連結"
          onChange={e => {
            this.parseNewImgURL(e.currentTarget.value);
          }}
        >
          <input />
          <Button
            icon={this.state.imageValid ? "checkmark" : "close"}
            disabled={this.state.imageValid ? false : true}
            color={this.state.imageValid ? "green" : null}
            onClick={this.addValidImage.bind(this)}
            type="submit"
          >
            加入
          </Button>
        </Input>

        <img
          hidden
          src={this.state.currentImageURL}
          onLoad={this.handleImgLoaded.bind(this)}
          alt=""
        />
      </span>
    );
  }
}



