import { DataObject } from "../../database/DataModel";
import { DatabaseTableURL } from "../../engine/Firebase/DatabaseTableURL";

import { observable, toJS } from "mobx";
import {
  MaterialObject,
  MaterialDetailModel
} from "../../database/MaterialModel";
import { DataStore, JoinTableProps, DataPack } from "./DataStore";
import { userStore } from "./UserStore";
import { createMaterialStore } from "../Material/Create/CreateMaterialStore";

export enum MaterialOrder {
  created_at,
  like_count,
  practiced_count
}
export enum MaterialFilter {
  mycreation,
  mycollection,
  all
}
export class MaterialStore extends DataStore {
  @observable materials: MaterialObject[] = [];
  @observable currentMaterial: MaterialObject;

  constructor() {
    super();
    this.generateFakeMaterial(10);
    this.firebaseURL = DatabaseTableURL.Material;
  }

  //get detail!
  checkCurrentMaterial(key: string) {
    if (this.currentMaterial == null) {
      console.log(key);
      this.currentMaterial = new MaterialObject();
      this.getMaterialBrief(this.currentMaterial, key);
      this.getMaterialDetail(this.currentMaterial, key);
    } else {
      if (this.currentMaterial.detail == null)
        this.getMaterialDetail(this.currentMaterial, key);
    }
  }
  prevImage() {
    if (this.currentMaterial.currentImgIndex > 0)
      this.currentMaterial.currentImgIndex--;
    else
      this.currentMaterial.currentImgIndex =
        this.currentMaterial.detail.imgURLs.length - 1;
  }
  nextImage() {
    var index = this.currentMaterial.currentImgIndex;
    if (index < this.currentMaterial.detail.imgURLs.length - 1)
      this.currentMaterial.currentImgIndex++;
    else this.currentMaterial.currentImgIndex = 0;
  }

  private getMaterialBrief(obj: MaterialObject, key: string) {
    return this.getObject(
      DatabaseTableURL.Material + "/brief/",
      key
    ).then(model => {
      obj.brief = model;
    });
  }

  private getMaterialDetail(obj: MaterialObject, key: string) {
    return this.getObject(
      DatabaseTableURL.Material + "/detail/",
      key
    ).then(model => {
      obj.detail = model;
    });
  }

  //filter
  //createby -> user
  //orderby -> like

  getMaterialForGrid(filter: MaterialFilter, orderby: MaterialOrder) {
    //TODO: get user collected material
    //TODO: get user created material
    console.log(filter);
    console.log(orderby);
    this.materials = [];
    if (filter == MaterialFilter.mycreation) {
      this.getMaterials("created_by", userStore.currentUser.uid);
    } else {
      this.getMaterials(orderby.toString());
    }
  }

  //get brief array and assign to variables
  private getMaterials(orderby: string, equalsTo?: string) {
    this.getBriefDataObjectArray(
      DatabaseTableURL.Material,
      orderby,
      equalsTo
    ).then(objs => {
      this.materials = objs;
    });
  }

  // getMaterialFromKeys(keys: string[]) {
  //   keys.forEach(key => {
  //     this.getObject(key, {
  //       url: DatabaseTableURL.Material,
  //       property: "materials",
  //       type: MaterialObject,
  //       model: "brief"
  //     });
  //   });
  // }

  //TODO:
  //set /:material/:key

  /**
   * Create Material
   */
  createMaterial(title) {
    var material = new MaterialObject();
    //material.detail = new MaterialDetailModel();
    material.brief.created_by = userStore.currentUser.uid;
    material.brief.created_at = Date.now();
    material.brief.title = title;

    console.log(material);
    createMaterialStore.material = material;
    return this.createObject(DatabaseTableURL.Material, material);
  }
  updateMaterialImages(material: MaterialObject, imgUrls: string[]) {
    material.brief.coverImgURL = imgUrls[0];
    material.detail = new MaterialDetailModel();
    material.detail.imgURLs = toJS(imgUrls);
    return this.updateObject(DatabaseTableURL.Material, material);
  }
  /**
   * Delete Material
   * auth:creator
   */
  //TODO:
  //only the user created the material can delete
  //delete /:material/:key
  //delete user_materialcollected??
  deleteMaterial(material: MaterialObject) {
    if (userStore.currentUser.uid != material.brief.created_by) {
      console.log("this user is not the owner of this material");
    } else {
    }
  }

  /**
   * Update Material 更新
   */

  // updateCurrentMaterial() {
  //   return this.updateObject(
  //     this.firebaseURL + "/" + this.currentMaterial.key,
  //     this.currentMaterial
  //   );
  // }

  searchByTag(tag: string) {}

  //NOT-TESTED:
  //user_collect_material_table
  addToCollection(material: MaterialObject) {
    this.currentMaterial = material;
    this.createJoinTableEntry(this.UserMaterialJoinProps("collect"));
  }
  removeFromCollection(material: MaterialObject) {
    this.removeJoinTableEntry(this.UserMaterialJoinProps("collect"));
  }
  isUserCollectMaterial(material: MaterialObject) {
    this.getJoinTableEntry("user_collect_material", material, "collectByUser");
  }

  UserMaterialJoinProps(behavior: string) {
    return new JoinTableProps(
      "user",
      userStore.currentUser.key,
      "material",
      this.currentMaterial.key,
      behavior
    );
  }

  //NOT-TESTED: user_like_material_table
  addToLike() {
    return this.createJoinTableEntry(this.UserMaterialJoinProps("like"));
  }

  //TODO:
  removeFromLike() {
    // return this.removeJoinTableEntry(
    //   DatabaseTableURL.user_like_material,
    //   userStore.currentUser.key
    // );
  }

  //Fake functions
  fakeMaterial: Array<MaterialObject>;
  generateFakeMaterial(n: number) {
    this.fakeMaterial = new Array<MaterialObject>();

    for (var i = 0; i < n; i++) {
      this.fakeMaterial.push(new MaterialObject());
    }
    return this.fakeMaterial;
  }
}

export var materialStore = new MaterialStore();



