import { observer } from "mobx-react";
import { webViewStore } from "./store/WebViewStore";
import * as React from "react";
import * as ReactDOM from "react-dom";
import * as Redux from "redux";

import {
  HashRouter as Router,
  Route,
  Link,
  Switch,
  RouteComponentProps
} from "react-router-dom";
import { Nav } from "./browser/Nav";
import { DatabaseTableURL } from "../engine/Firebase/DatabaseTableURL";
import { MaterialPageView } from "./Material/View/Page/MaterialPageView";
import { MaterialMenuView } from "./Material/View/Menu/MaterialMenuView";
import { DashBoardView } from "./User/DashBoardView";
import { userStore } from "./store/UserStore";
import { Home } from "./Home/Home";
import { Container, Button, Popup } from "semantic-ui-react";
import glamorous from "glamorous";
import { DrawSketchView } from "./PaintTool/DrawSketchView";
import { SketchPageView } from "./Sketch/SketchPageView";
import { Component } from "react";
import { withRouter } from "react-router";
import FirebaseManager from "../engine/Firebase/FirebaseManager";

export var firebaseManager: FirebaseManager = new FirebaseManager();
class ScrollToTop extends React.Component<any, any> {
  componentDidUpdate(prevProps) {
    if (this.props.location !== prevProps.location) {
    }
  }
  render() {
    return this.props.children;
  }
}

export default withRouter(ScrollToTop);
export const MainContainer = glamorous(Container)(
  webViewStore.isNavShow ? { marginTop: "7em" } : { marginTop: 0 }
);

@observer
class SketchShare extends React.Component<any, any> {
  render() {
    return (
      <div>
        <Router>
          <div>
            {webViewStore.isNavShow ? <Nav /> : null}

            <Switch>
              <Route
                path={DatabaseTableURL.Material + "/:key"}
                component={MaterialPageView}
              />
              <Route
                path={DatabaseTableURL.Material}
                component={MaterialMenuView}
              />

              <Route
                path={DatabaseTableURL.Sketch + "/:key"}
                component={SketchPageView}
              />
              <Route path="/paintCanvas/:key" component={DrawSketchView} />
              <Route path={DatabaseTableURL.Me} component={DashBoardView} />
              <Route path="/" component={Home} />
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

ReactDOM.render(<SketchShare />, document.getElementById("paint-tool"));
