import {
  IBinaryData,
  UserCommand,
  UserCommandType
} from "../event/UserCommand";
import ByteBuffer from "../../bytebuffer";
import { paintToolManager } from "../painter";
export default class SAClip implements IBinaryData {
  notes = new Array<SANote>();
  atStroke: number;
  //undo commands exclusive (not rendered)
  renderCommands: UserCommand[] = [];
  //所有的指令，include undo redo
  // fullCommands: UserCommand[] = [];
  //the commands temp stored
  undoCommands: UserCommand[] = [];
  currentCommandIndex: number;
  userCommands: { [user: number]: UserCommand[] };

  constructor(at: number) {
    this.atStroke = at;
    this.currentCommandIndex = 0;
  }
  addUser(user: number) {
    this.userCommands[user] = [];
  }
  addCommand(cmd: UserCommand) {
    if (this.fullCommands.length == 0 && cmd.type == UserCommandType.tool) {
      paintToolManager.userChangeToolCommand();
    }
    this.fullCommands.push(cmd);
    this.currentCommandIndex++;
    //this.renderCommands.push(cmd);
  }
  // exportArray(): number[] {
  //   var array: number[] = [];
  //   array.push(this.fullCommands.length);
  //   this.fullCommands.forEach(command => {
  //     array.push.apply(array, command.exportArray());
  //   });
  //   return array;
  // }

  dataSize() {
    var size = 0;
    this.fullCommands.forEach(command => {
      size += command.dataSize();
    });
    //length,fullcommands
    return 4 + size;
  }
  writeToData(byteBuffer: ByteBuffer) {
    //commands length
    //[commands]
    byteBuffer.writeInt(this.fullCommands.length);
    this.fullCommands.forEach(command => {
      command.writeToData(byteBuffer);
    });
  }
  static fromData(byteBuffer: ByteBuffer) {
    var clip = new SAClip(0);
    //commands length
    //[commands]
    var commandLength = byteBuffer.readInt();
    for (var i = 0; i < commandLength; i++) {
      var command = UserCommand.fromData(byteBuffer);
      clip.addCommand(command);
    }
    return clip;
  }
}



