import { observable } from "mobx";
import { MaterialObject } from "../../../database/MaterialModel";
import { materialStore } from "../../store/MaterialStore";
import * as firebase from "firebase";
import { firebaseManager } from "../../index";
export class ImageRef {
  type: "url" | "upload";
  url: string;
  data: string;
  constructor(type: "url" | "upload", str: string) {
    this.type = type;
    if (type == "url") {
      this.url = str;
    }
    this.data = str;
  }
  uploadToStorage(key: string, id: string) {
    //TODO:  need image compression
    if (this.type == "upload") {
      console.log("upload image");
      return firebaseManager.storageManager
        .uploadImage("materialImages/" + key, id, this.data)
        .then(a => {
          this.url = a;
        });
    }
  }
}
var isTesting = false;
class CreateMaterialStore {
  @observable isModalOpen: boolean = false;
  @observable status?: number = 0;
  @observable tabIndex = 0;
  @observable images: ImageRef[] = [];
  title: string = "defaultTitle";
  description: string = "defaulDescription";
  material: MaterialObject;

  addImage(imageRef: ImageRef) {
    this.images.push(imageRef);
  }
  //https://p2.bahamut.com.tw/S/2KU/66/0001594066.JPG
  createMateiral() {
    console.log("start create");

    var promises: Promise<any>[] = [];
    var urls: string[] = [];

    return materialStore.createMaterial(this.title).then(key => {
      this.material.key = key;
      this.images.forEach((image, id) => {
        //Upload image to storage if type = data
        //TODO:  need image compression
        promises.push(image.uploadToStorage(key, id.toString()));
      });
      return Promise.all(promises).then(() => {
        console.log("all image uploaded");

        this.images.forEach(image => {
          urls.push(image.url);
        });
        return materialStore.updateMaterialImages(this.material, urls);
      });
    });
  }
  closeModal() {
    this.isModalOpen = false;
    this.status = 0;
    this.images = [];
    this.title = "default";
    this.description = "default";
  }

  previewUploadImagePath(path) {
    let reader = new FileReader();
    reader.onloadend = () => {
      createMaterialStore.addImage(new ImageRef("upload", reader.result));
      //Test:
      if (isTesting) {
        this.createMateiral();
      }
      //Test:
    };
    reader.readAsDataURL(path);
  }
}
export var createMaterialStore = new CreateMaterialStore();



