import { createMaterialStore } from "./CreateMaterialStore";
import * as React from "react";
import { CreateMaterialModal } from "./CreateMaterialModal";
import glamorous from "glamorous";
import { userStore } from "../../store/UserStore";
import {
  Menu,
  Button,
  Modal,
  Header,
  Image,
  Icon,
  Tab,
  Input,
  Popup,
  Grid,
  Step,
  Item
} from "semantic-ui-react";
import { LoginModal } from "../../browser/LoginModal";
import { observer } from "mobx-react";

export const FixedButton = glamorous(Button)({
  position: "fixed",
  right: "20px",
  bottom: "20px"
});

@observer
export class CreateMaterialPopUp extends React.Component<
  any,
  { open: boolean }
> {
  constructor() {
    super();
    this.state = { open: false };
  }
  handleOpen = () => {
    this.setState({ open: true });
  };
  handleClose = () => {
    if (!createMaterialStore.isModalOpen) this.setState({ open: false });
  };
  render() {
    return (
      <div>
        {userStore.currentUser == null
          ? <LoginModal
              info="建立需登入"
              triggerComponent={
                <FixedButton icon primary circular size="large">
                  <Icon name="plus" />{" "}
                </FixedButton>
              }
            />
          : <Popup
              wide
              hoverable
              position="left center"
              on="click"
              open={this.state.open}
              onOpen={this.handleOpen}
              onClose={this.handleClose}
              closeOnTriggerMouseLeave={false}
              closeOnEscape={false}
              closeOnPortalMouseLeave={false}
              trigger={
                <FixedButton icon primary circular size="large">
                  <Icon name="plus" />{" "}
                </FixedButton>
              }
            >
              <Grid divided columns="equal">
                <Grid.Row>
                  <CreateMaterialModal
                    trigger={
                      <Button
                        onClick={() => (createMaterialStore.tabIndex = 0)}
                      >
                        <Icon name="pin" />
                        網路圖片連結
                      </Button>
                    }
                    onClose={() => {
                      this.setState({ open: false });
                    }}
                  />
                </Grid.Row>
                <Grid.Row>
                  <CreateMaterialModal
                    trigger={
                      <Button
                        onClick={() => (createMaterialStore.tabIndex = 1)}
                      >
                        <Icon name="upload" />
                        上傳圖片
                      </Button>
                    }
                    onClose={() => {
                      this.setState({ open: false });
                    }}
                  />
                </Grid.Row>
              </Grid>
            </Popup>}
      </div>
    );
  }
}



