import { SemanticWIDTHS } from "semantic-ui-react/src";
import * as React from "react";
import { MaterialCell } from "./MaterialCell";
import { observer } from "mobx-react";
import * as semanticUIReact from "semantic-ui-react";
import { DataModel } from "../../../../database/DataModel";

import styled from "styled-components";
import glamorous, { ThemeProvider } from "glamorous";
import { materialStore } from "../../../store/MaterialStore";

const options = [
  { key: "熱門度", text: "熱門度", value: "viewCount" },
  { key: "最多練習", text: "最多練習", value: "最多練習" },
  { key: "色調", text: "色調", value: "色調" }
];
const MyStyledDiv = glamorous.div({
  width: 10,
  fontSize: 20,
  textAlign: "center",
  padding: 10
});
@observer
export class MaterialMenuView extends React.Component<any, { sortby: string }> {
  componentWillMount() {
    //  this.itemArray = new Array<TutorialStepByStepData>()
    // var data = new TutorialStepByStepData()
    // data.author= "Jerry"
    // data.title = "測ssww試"
    // data.coverImgURL="http://4.bp.blogspot.com/-kCmsPEz_ptc/T-KeN-aUg7I/AAAAAAAAB2M/8lA4QNO-mhw/s1600/Draw+Cat+(6).jpg"
  }

  receiveMaterials(val: any) {}
  changeFilter(filter: string) {
    //DataModel.sort(materialStore.materialCollections, filter);
  }

  render() {
    return (
      <div>
        <p>
          <div className="ui raised very padded text container segment">
            <h2 className="ui header">每日熱門素材</h2>
            <p />
            <p />
          </div>
        </p>
        <semanticUIReact.Dropdown
          placeholder="時間"
          fluid
          multiple
          selection
          options={options}
          onChange={(e, d) => this.changeFilter(d.value as string)}
        />
        <div className="ui four column doubling stackable grid container">
          {materialStore.materials.map(function(value) {
            return (
              <div>
                <MaterialCell data={value} />
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}



