import * as React from "react";
import { MaterialRefView } from "./MaterialRefView";
import { MySideBar } from "./sidebar";
import {
  initCanvas,
  mouseEventHandler
} from "../../engine/painter/painter";
import { RouteComponentProps } from "react-router-dom";
import { materialStore } from "../store/MaterialStore";
import { webViewStore } from "../store/WebViewStore";
import { observer } from "mobx-react";
import * as ReactDOM from "react-dom";
import { PaintCanvas } from "./PaintCanvas";
import {
  paintToolManager,
  glContextBuffer
} from "../../engine/painter/painter";
import {
  Button,
  Icon,
  Sidebar,
  Segment,
  Menu,
  Header
} from "semantic-ui-react";
import { Prompt } from "react-router";
import { RefImageWindow } from "../Material/component/RefImageWindow";
import { paintCanvasStore } from "../store/PaintCanvasStore";

//TODO:
//View mode and Draw Mode
@observer
export class DrawSketchView extends React.Component<
  RouteComponentProps<any>,
  { page: number }
> {
  componentWillMount() {
    var key = this.props.match.params.key;
    materialStore.checkCurrentMaterial(key);
  }
  componentDidMount() {
    webViewStore.isNavShow = false;
    mouseEventHandler.painterInit();
    //this.leavePage();
  }
  componentWillUnmount() {
    webViewStore.isNavShow = true;
    mouseEventHandler.painterExit();
  }
  saveImage() {
    var img = gl.canvas.toDataURL("image/png");
    console.log(img);
    var url = img.replace(/^data:image\/[^;]/, "data:application/octet-stream");
    window.open(url);
  }
  leavePage() {
    window.addEventListener("beforeunload", function(e) {
      var confirmationMessage =
        "It looks like you have been editing something." +
        "If you leave before saving, your changes will be lost.";

      (e || window.event).returnValue = confirmationMessage; //Gecko + IE
      return confirmationMessage; //Gecko + Webkit, Safari, Chrome etc.
    });
  }
  canvas: HTMLCanvasElement;
  render() {
    return (
      <div>
        {materialStore.currentMaterial &&
          materialStore.currentMaterial.detail &&
          <RefImageWindow width={100} height={100} />}
        {/* <div className="progress-bar">
            <div className="critique-list">
              <div className="critique-tag" data-percent="60%" />
              <div className="critique-tag" data-percent="30%" />
              <div className="critique-tag" data-percent="70%" />
            </div>
            <div
              className="ui teal progress"
              data-percent="20"
              id="progressbar"
            >
              <div className="bar">
                <div className="progress" />
              </div>
            </div>
          </div> */}
        {/* <Sidebar.Pushable as={Segment}>
          <Sidebar
            as={Menu}
            animation="uncover"
            width="very thin"
            visible={true}
            //icon="labeled"
            vertical
            icon="labeled"
            inverted
          >
            
          </Sidebar>
          <Sidebar.Pusher>
            
          </Sidebar.Pusher>
        </Sidebar.Pushable> */}

        <Menu
          inverted
          vertical
          icon="labeled"
          style={{ position: "fixed", zIndex: "100" }}
        >
          <Menu.Item
            name="home"
            onClick={() => {
              paintToolManager.usePencil();
            }}
          >
            <Icon name="pencil" />Games
          </Menu.Item>
          <Menu.Item
            name="home"
            onClick={() => {
              paintToolManager.useEraser();
            }}
          >
            <Icon name="eraser" />
          </Menu.Item>
          <Menu.Item name="gamepad" onClick={() => {}}>
            <Icon name="magnify" />
          </Menu.Item>
          <Menu.Item name="camera" onClick={() => this.saveImage()}>
            <Icon name="camera" />
          </Menu.Item>
          <Menu.Item>
            <p>
              {paintCanvasStore.scale.toPrecision(2)}
            </p>
          </Menu.Item>
          <Menu.Item name="camera" onClick={() => this.saveImage()}>
            <Icon name="lastStep" />
          </Menu.Item>
        </Menu>

        <PaintCanvas />

        <Prompt when={true} message="還沒儲存喔！離開你的畫作就會永遠消失了" />
      </div>
    );
  }
}



