import {
  sc_h,
  sc_w,
  paintToolManager,
  mouseEventHandler,
  textureManager
} from "../painter";
import GLStrokeRenderer from "./GLStrokeRenderer";
import { Layer } from "./canvas/Layer";
import { PaintBrush } from "../PaintTool/PaintBrush";
import { TextureManager } from "../PaintTool/TextureManager";
import { paintCanvasStore } from "../../../components/store/PaintCanvasStore";

export class GLContextBuffer {
  layers: Array<Layer>;
  bgLayer: Layer;
  width: number;
  height: number;
  currentLayer: Layer;
  renderTexture_vsh = document.getElementById("vertex-renderTexture");
  renderTexture_fsh = document.getElementById("fragment-renderTexture");
  drawLayerShader = new GL.Shader(
    this.renderTexture_vsh.textContent,
    this.renderTexture_fsh.textContent
  );
  private pencil_vert = document.getElementById("pencil-vertex");
  private pencil_frag = document.getElementById("pencil-fragment");
  pencilShader = new GL.Shader(
    this.pencil_vert.textContent,
    this.pencil_frag.textContent
  );
  strokeRenderer: GLStrokeRenderer;
  pMatrix: GL.Matrix;
  cursorMesh = GL.Mesh.plane({ coords: true });
  constructor(width: number, height: number) {
    this.width = width;
    this.height = height;
    console.log("init canvas+ width:" + width + "height:" + height);
    this.layers = new Array<Layer>();
    this.layers.push(new Layer(width, height));
    this.tempLayer = new Layer(width, height);
    this.bgLayer = new Layer(width, height);
    this.cursorLayer = new Layer(width, height);
    var bgImg = new Image();
    this.currentLayer = this.layers[0];
    this.currentLayer.opacity = 1;
    //this.mesh.vertices = [[0, 0, 0], [1, 0, 0], [0, 1, 0], [1, 1, 0]];
    this.mesh.coords = [[0, 0], [1, 0], [0, 1], [1, 1]];
    this.mesh.compile();
    this.pMatrix = GL.Matrix.ortho(0, width, 0, height, -1, 1);
    //this.renderMVP = GL.Matrix.identity()
    this.strokeRenderer = new GLStrokeRenderer(
      this.currentLayer.texture,
      this.pMatrix
    );

    //context.bgLayer.setTexure(GL.Texture.fromURL('paper_sketch.png'))
    bgImg.src = "img/paper_sketch.png";
    bgImg.onload = () => {
      GLContextBuffer.isDirty = true;
      this.bgLayer.setTexure(GL.Texture.fromImage(bgImg));
      this.renderCanvas();
    };
    this.resizeCanvas(sc_w, sc_h);
  }

  tempLayer: Layer;
  cursorLayer: Layer;

  resize(canvas: HTMLCanvasElement) {
    // Lookup the size the browser is displaying the canvas.
    var displayWidth = canvas.clientWidth;
    var displayHeight = canvas.clientHeight;

    // Check if the canvas is not the same size.
    if (canvas.width != displayWidth || canvas.height != displayHeight) {
      // Make the canvas the same size
      canvas.width = displayWidth;
      canvas.height = displayHeight;
    }
  }
  resizeCanvas(sc_w: number, sc_h: number) {
    console.log(sc_w);

    var w = sc_w / this.width / 2;
    console.log(this.width);
    var h = sc_h / this.height / 2;
    this.renderMVP = GL.Matrix.ortho(-w, w, -h, h, -1, 1);

    this.canvasMVP = GL.Matrix.ortho(-w, w, -h, h, -1, 1);
    //var perspective = GL.Matrix.perspective(,w/h,0.1,100);
    //var view = GL.Matrix.lookAt(0,0,1,0,0,0,0,1,0);
    //this.renderMVP = GL.Matrix.multiply(view,this.renderMVP);

    //this.newScale = this.height/sc_h
    console.log(h);
    GLContextBuffer.isDirty = true;

    gl.viewport(0, 0, sc_w, sc_h);
    gl.viewport(0, 0, this.width, this.height);
  }
  readPixel(x: number, y: number) {
    //this.currentLayer.texture.bind(0);
    var result = this.currentLayer.texture.canDrawTo();
    if (!result) throw new Error("can read currentlayer texture");
    var framebuffer = gl.createFramebuffer();
    gl.bindFramebuffer(gl.FRAMEBUFFER, framebuffer);
    gl.framebufferTexture2D(
      gl.FRAMEBUFFER,
      gl.COLOR_ATTACHMENT0,
      gl.TEXTURE_2D,
      this.currentLayer.texture.id,
      0
    );
    var pixels = new Uint8Array(4);
    gl.readPixels(x, y, 1, 1, gl.RGBA, gl.UNSIGNED_BYTE, pixels);
    gl.bindFramebuffer(gl.FRAMEBUFFER, null);
    var f = new Array<number>(4);
    for (var i = 0; i < 4; i++) {
      f[i] = pixels[i] / 255;
    }
    this.currentLayer.texture.unbind(0);
    //console.log(pixels); // Uint8Array
    //pixels/=255;
    return f;
  }
  posX: number = 0;
  posY: number = 0;
  pan(x, y) {
    this.posX -= x;
    this.posY -= y;

    this.applyCss();
  }
  applyCss() {
    //gl.canvas.style.transform = "scale(" + this.newScale + ")";
    console.log(`scale(${this.newScale}) translateY(${this.posY}px)`);

    gl.canvas.style.transform = `matrix(${this.newScale},0,0,${this
      .newScale},${this.posX},${this.posY})`;
  }
  zoom(scale: number, pivotx: number, pivoty: number) {
    this.newScale *= scale;
    if (this.newScale < 0.1) this.newScale = 0.1;
    //gl.canvas.style.transform = "scale(" + this.newScale + ")";
    //gl.canvas.style.transformOrigin = `${pivotx*100}% ${pivoty*100}%`
    //console.log(`${pivotx*100}% ${pivoty*100}`)
    //TODO: zoom at pivot applytransform
    this.applyCss();

    //gl.canvas.parentElement.style.overflow = "initial";

    this.pivotx = pivotx;
    this.pivoty = pivoty;
    paintCanvasStore.scale = this.newScale;
    //this.cameraPosX = pivotx;
    //this.cameraPosY = pivoty;
    //console.log("" + pivotx + " " + pivoty);
    //GLContextBuffer.isDirty = true;
  }
  addLayer() {
    var i = this.layers.push(new Layer(this.width, this.height));
    this.currentLayer = this.layers[i - 1];
  }
  deleteLayer(index: number) {
    this.layers = this.layers.splice(index, 1);
  }
  moveLayer(index: number, to: number) {
    var tempLayer = this.layers.splice(index, 1);
    this.layers.splice(to, 0, tempLayer[0]);
  }
  setCurrentLayer(index: number) {
    this.currentLayer = this.layers[index];
  }
  changeBGTexture(texture: GL.Texture) {
    this.bgLayer.texture = texture;
  }
  clearCanvas() {
    this.currentLayer.texture.drawTo(() => {
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
      gl.clearColor(0, 0, 0, 0);
      gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);
    });
    this.cursorLayer.texture.drawTo(() => {
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
      gl.clearColor(0, 0, 0, 0);
      gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);
    });
    GLContextBuffer.isDirty = true;
  }
  canvasScale: number = 1;
  newScale: number = 1;

  //
  pivotx: number = 0;
  pivoty: number = 0;
  canvasPosX: number = 0;
  canvasPosY: number = 0;

  //只能做中心縮放和平移
  //TODO: scale on pivot
  calCameraMatrix() {
    var renderMVP = this.canvasMVP;

    var awayMatrix = GL.Matrix.translate(-this.pivotx, -this.pivoty, 0);
    var scaleMatrix = GL.Matrix.scale(this.newScale, this.newScale, 1);
    var backMatrix = GL.Matrix.translate(this.canvasPosX, this.canvasPosY, 0);

    //renderMVP = GL.Matrix.multiply(GL.Matrix.translate(-this.cameraPosX,-this.cameraPosY,0),renderMVP);
    //renderMVP = GL.Matrix.multiply(awayMatrix,renderMVP);

    renderMVP = GL.Matrix.multiply(scaleMatrix, renderMVP);

    renderMVP = GL.Matrix.multiply(backMatrix, renderMVP);

    this.renderMVP = renderMVP;
    this.canvasScale = this.newScale;
    paintCanvasStore.scale = this.canvasScale;
    this.renderMVP = GL.Matrix.identity();
  }

  static isDirty: boolean;

  /*重新render：
  1. 清除
  2. 計算3D transfrom Matrix
  3. 畫背景
  4.
  */
  renderCanvas() {
    if (!GLContextBuffer.isDirty) return;
    //1.
    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
    gl.clearColor(0, 0, 0, 0);
    gl.enable(gl.BLEND);
    gl.blendEquation(gl.FUNC_ADD);
    gl.blendFunc(gl.ONE, gl.ONE_MINUS_SRC_ALPHA);
    //2.
    //this.calCameraMatrix();

    //TODO:draw cursor

    //3.
    this.drawRenderLayer(this.bgLayer, this.renderMVP);

    //複製圖層部分區域
    this.copySubImage(this.layers[0], this.tempLayer, 0, 0.5, 0, 0.5);
    //100/gl.canvas.width,100/gl.canvas.height

    //4.
    // if (!this.layers[0].isHidden)
    this.drawRenderLayer(this.layers[0], this.renderMVP);
    //this.drawRenderLayer(this.cursorLayer, this.renderMVP);
    this.drawCursorDirect();
    //this.drawRenderLayer(this.tempLayer);
    /*
    for(var i = 0 ;i<this.layers.length;i++)
    {
        this.drawRenderLayer(this.layers[i]);
    }*/

    GLContextBuffer.isDirty = false;
  }
  renderMVP: GL.Matrix;
  canvasMVP: GL.Matrix;

  mesh = GL.Mesh.plane({ coords: true });

  //複製矩形範圍到另一個texture
  copySubImage(
    src: Layer,
    dst: Layer,
    x1: number,
    x2: number,
    y1: number,
    y2: number
  ) {
    var mesh = GL.Mesh.plane({ coords: true });
    //mesh.vertices = [[x1*gl.canvas.width, y1*gl.canvas.height, 0], [x2*gl.canvas.width, y1*gl.canvas.height, 0], [x1*gl.canvas.width, y2*gl.canvas.height, 0], [x2*gl.canvas.width, y2*gl.canvas.height, 0]];
    //mesh.vertices = [[x1, y1, 0], [x2, y1, 0], [x1, y2, 0], [x2, y2, 0]];
    //mesh.vertices = [[0, 0, 0], [1, 0, 0], [0, 1, 0], [1,1, 0]];

    //TODO: vertices coordinate is -1,-1, 1,1, 1,-1,-1,1
    mesh.vertices = [
      [x1 * 2 - 1, y1 * 2 - 1, 0],
      [x2 * 2 - 1, y1 * 2 - 1, 0],
      [x1 * 2 - 1, y2 * 2 - 1, 0],
      [x2 * 2 - 1, y2 * 2 - 1, 0]
    ];
    //console.log(mesh.vertices)
    mesh.coords = [[x1, y1], [x2, y1], [x1, y2], [x2, y2]];
    mesh.compile();
    var ctx = this;
    dst.texture.drawTo(function() {
      gl.enable(gl.BLEND);
      gl.blendEquation(gl.FUNC_ADD);
      gl.blendFunc(gl.ONE, gl.ONE_MINUS_SRC_ALPHA);
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
      gl.clearColor(0, 0, 0, 0);
      src.texture.bind(0);
      ctx.drawLayerShader
        .uniforms({
          texture: 0,
          mvp: GL.Matrix.identity(),
          opacity: [1.0]
        })
        .draw(mesh);
      src.texture.unbind(0);
      gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);
    });
  }
  drawRenderLayer(layer: Layer, mvp: GL.Matrix) {
    var a = new Float32Array([layer.opacity]);
    if (layer.isHidden) return;
    layer.texture.bind(0);
    this.drawLayerShader
      .uniforms({
        texture: 0,
        mvp: mvp,
        opacity: a[0]
      })
      .draw(this.mesh);
    layer.texture.unbind(0);
    //layer.texture.unbind(0);
  }
  drawCursor() {
    this.strokeRenderer.drawCursor(this.cursorLayer.texture);
    GLContextBuffer.isDirty = true;
  }
  drawCursorDirect() {
    var brush = paintToolManager.circleCursor;

    if (!mouseEventHandler.lastScreenPoint) return;
    var mesh = this.cursorMesh;
    var p = mouseEventHandler.lastScreenPoint;
    var size = paintToolManager.userTool[0].size;
    //var size = paintToolManager.currentTool.size * this.newScale;
    var size_w = size / sc_w;
    var size_h = size / sc_h;

    mesh.vertices = [
      [p.x - size_w, p.y - size_h, 0, 1],
      [p.x + size_w, p.y - size_h, 0, 1],
      [p.x - size_w, p.y + size_h, 0, 1],
      [p.x + size_w, p.y + size_h, 0, 1]
    ];

    mesh.compile();

    var texture = textureManager.getTexture(brush.textureName);
    texture.bind(0);
    brush.shader
      .uniforms({
        mvp: GL.Matrix.identity(),
        tex0: 0,
        border: 0.01,
        size: size
      })
      .draw(mesh);
    texture.unbind(0);
  }
}



