export class DataModel {
  constructor() {}

  copyFromJSONObject(jobj: any) {
    for (var prop in jobj) this[prop] = jobj[prop];
  }
  static sort(
    dataModels: DataModel[],
    propertyName: string,
    order: SortOrder = SortOrder.Descend
  ) {
    dataModels.sort(function(a: DataModel, b: DataModel) {
      var a_property = Object.getOwnPropertyDescriptor(a, propertyName);
      var b_property = Object.getOwnPropertyDescriptor(b, propertyName);
      var result = a_property.value - b_property.value;
      if (order == SortOrder.Descend) return result;
      else return -result;
    });
    return dataModels;
  }
}

enum SortOrder {
  Descend,
  Ascend
}

function dataModelFromJSONTest() {}

export class DataObject {
  key: string;
  brief: DataModel;
  detail: DataModel;
}



