import BrushShaderManager from "../stroke/BrushShaderManager";
import { PaintBrush, BrushBlendType } from "./PaintBrush";
import { paintRecorder } from "../painter";
import { userStore } from "../../../components/store/UserStore";
import { ToolType } from "../event/UserCommand";
import {
  UserCommand,
  UserCommandType,
  ToolType,
  ToolCommandData,
  ToolAttributeCommandData,
  ToolAttributeType
} from "../event/UserCommand";
export default class PaintToolManager {
  currentBrush: PaintBrush;
  eraser: PaintBrush;
  //currentTool: PaintBrush;
  userTool: { [user: number]: PaintBrush } = {};
  get myTool() {
    return this.userTool[userStore.myUserID];
  }
  setMyTool(type: ToolType) {
    this.userChangeToolCommand(type, userStore.myUserID);
  }
  cursor: PaintBrush;

  dirty: boolean = false;
  brushDict: { [id: string]: PaintBrush } = {};
  pencil: PaintBrush;
  brush: PaintBrush;
  flatPen: PaintBrush;

  circleCursor: PaintBrush;

  _size: ToolAttributeCommandData;
  constructor() {
    var brushShaderManager = new BrushShaderManager();
    var flatShader = brushShaderManager.vertexShader;
    var pencilShader = brushShaderManager.pencilShader;
    this.pencil = new PaintBrush(ToolType.pencil, "pencil", pencilShader);
    this.pencil.setAttribute(1, 2);
    this.brush = new PaintBrush(ToolType.pointPen, "circle", flatShader);
    this.brush.setAttribute(100, 0.1);
    this.eraser = new PaintBrush(
      ToolType.eraser,
      "circle",
      flatShader,
      BrushBlendType.eraser
    );
    this.eraser.setAttribute(100, 0.01);
    this.cursor = new PaintBrush(ToolType.cursor, "circle", flatShader);
    this.cursor.setAttribute(20, 0.1);
    this.circleCursor = new PaintBrush(
      ToolType.cursor,
      "circle",
      brushShaderManager.cursorCircleShader
    );
    this.circleCursor.size = 20;
    this.toolDict = {};
    this.toolDict[ToolType.pencil] = this.pencil;
    this.toolDict[ToolType.eraser] = this.eraser;
  }
  setDefaultTool() {
    this.userChangeToolCommand(ToolType.pencil, userStore.myUserID);
  }

  // useTool(tool?: "pencil" | "eraser") {
  //   this.currentTool = this.pencil;
  // }

  //TODO: use dictionary to save tools
  //Dictionary of type and brush
  //ref: https://stackoverflow.com/questions/13631557/typescript-objects-as-dictionary-types-as-in-c-sharp
  toolDict: { [toolType: number]: PaintBrush };

  excuteCommand(cmd: UserCommand) {
    var type = (cmd.commandData as ToolCommandData).type;
    this.userTool[cmd.user] = this.toolDict[type];
  }
  userChangeToolCommand(type: ToolType, user: number) {
    console.warn("ToolTypeChange");
    //Check the tool not used
    if (this.userTool[user] == this.toolDict[type]) return;
    this.recordTool(type, user);
    //TODO:send to firebase
    //...
  }
  recordTool(type: ToolType, user: number) {
    var cmdData = new ToolCommandData(type);
    var command = new UserCommand(UserCommandType.tool, cmdData, user);
    paintRecorder.addCommand(command);
    this.excuteCommand(command);
  }
  setDirty() {
    this.dirty = true;
  }

  sizeCommand: UserCommand;
  setSize(val: number, user: number) {
    this.sizeCommand.timestamp = Date.now();
    this.sizeCommand.commandData = new ToolAttributeCommandData(
      ToolAttributeType.size,
      val
    );
    this.userTool[user].size = val;
  }
  /*
    set color(val:Color)
    {
        this.currentTool.color = val;
        this.currentTool.color.a = this.opacity;
        this.setDirty();
    }*/
  set opacity(val: number) {
    this.setDirty();
  }
  set mixColor(
    val: number //1~100?
  ) {}

  /*
    checkDirty():any
    {
        var array = new Array<UserCommand>()
        this.commands.forEach(command => {
            
        });
        //this.sizeCommand.
    }*/
  reset() {
    this.dirty = false;
  }
}



