import { IBinaryData } from '../event/UserCommand';
import ByteBuffer from '../../bytebuffer';
import { PointData } from './PaintPoint';

export default class PaintStroke implements IBinaryData{
    writeToData(byteBuffer:ByteBuffer) {
        
        //point length
        //[points]
        byteBuffer.writeInt(this.points.length)
        this.points.forEach(point=>{
            point.writeToData(byteBuffer)
        })
    }
    static fromData(byteBuffer:ByteBuffer) {
        //point length
        //[points]
        var len = byteBuffer.readInt()
        var stroke = new PaintStroke()
        stroke.points = []
        for (var i = 0; i < len; i++) {
            var point = PointData.fromData(byteBuffer)
            stroke.points.push(point)
        }
        return stroke
    }

    dataSize(): number {
        var size = 0
        this.points.forEach(element => {
            size += element.dataSize
        });
       return size
    }
    points: PointData[] = [];
    constructor() {
    }
    addPoints(points:PointData[])
    {
        this.points.push.apply(this.points,points);
    }

    //len
    //points
    exportArray()
    {
        var array:number[] =[];
        array.push(this.points.length);
        for(var i=0;i<this.points.length;i++)
        {
            array.push.apply(array,this.points[i].exportArray)
        }
        return array
    }
    parseData()
    {

    }   
}



