export default class ByteBuffer {
  // Byte order constants
  static LITTLE_ENDIAN = true;
  static BIG_ENDIAN = false;

  // Creates a new ByteBuffer
  // - from given source (assumed to be number of bytes when numeric)
  // - with given byte order (defaults to big-endian)
  // - with given implicit growth strategy (defaults to false)
  private _buffer: ArrayBuffer;
  private _raw: Uint8Array;
  private _view: DataView;
  private _order;
  private _implicitGrowth: boolean;
  private _index = 0;
  constructor(source: ArrayBuffer | number, order = ByteBuffer.BIG_ENDIAN) {
    // Holds buffer
    this._buffer = null;

    // Holds raw buffer
    this._raw = null;

    // Holds internal view for reading/writing
    this._view = null;

    // Holds byte order
    this._order = !!order;

    // Holds implicit growth strategy
    this._implicitGrowth = true;

    // Holds read/write index
    this._index = 0;

    // Attempt to extract a buffer from given source
    var buffer: ArrayBuffer;

    if ((source as ArrayBuffer).byteLength != undefined) {
      console.log("normal buffer");
      buffer = source as ArrayBuffer; // this._extractBuffer(source as ArrayBuffer, true);
    } else {
      console.log("buffer length");
      buffer = new ArrayBuffer(source as number);
    }

    // On failure, assume source is a primitive indicating the number of bytes
    //if (!buffer) {

    // Assign new buffer
    this.buffer = buffer;
  }

  // Sanitizes read/write index
  _sanitizeIndex() {
    if (this._index < 0) {
      this._index = 0;
    }
    if (this._index > this.length) {
      this._index = this.length;
    }
  }

  // Extracts buffer from given source and optionally clones it
  _extractBuffer(source: ArrayBuffer, clone = false) {
    // Whether source is a byte-aware object
    if (source && typeof source.byteLength !== "undefined") {
      // Determine whether source is a view or a raw buffer
      // if (typeof source.buffer !== "undefined") {
      //   return clone ? source.buffer.slice(0) : source.buffer;
      // }
      // return clone ? source.slice(0) : source;
      // Whether source is a sequence of bytes
    } else if (source && typeof source.byteLength !== "undefined") {
      // Although Uint8Array's constructor succeeds when given strings,
      // it does not correctly instantiate the buffer
      if (source.constructor === String) {
        return null;
      }

      try {
        return new Uint8Array(source).buffer;
      } catch (error) {
        return null;
      }

      // No buffer found
    } else {
      return null;
    }
  }

  // Retrieves buffer
  get buffer() {
    return this._buffer;
  }

  // Sets new buffer and sanitizes read/write index
  set buffer(buffer: ArrayBuffer) {
    console.log(buffer);
    this._buffer = buffer;
    this._raw = new Uint8Array(this._buffer);
    this._view = new DataView(this._buffer);
    this._sanitizeIndex();
  }

  // Retrieves raw buffer
  get raw() {
    return this._raw;
  }

  // Retrieves view
  get view() {
    return this._view;
  }

  // Retrieves number of bytes
  get length() {
    return this._buffer.byteLength;
  }

  // Retrieves number of bytes
  // Note: This allows for ByteBuffer to be detected as a proper source by its own constructor
  get byteLength() {
    return this.length;
  }

  // Retrieves byte order
  get order() {
    return this._order;
  }

  // Sets byte order
  set order(order) {
    this._order = !!order;
  }

  // Retrieves implicit growth strategy
  get implicitGrowth() {
    return this._implicitGrowth;
  }

  // Sets implicit growth strategy
  set implicitGrowth(implicitGrowth) {
    this._implicitGrowth = !!implicitGrowth;
  }

  // Retrieves read/write index
  get index() {
    return this._index;
  }

  // Sets read/write index
  set index(index) {
    if (index < 0 || index > this.length) {
      throw new RangeError(
        "Invalid index " + index + ", should be between 0 and " + this.length
      );
    }

    this._index = index;
  }

  // Sets index to front of the buffer
  front() {
    this._index = 0;
    return this;
  }

  // Sets index to end of the buffer
  end() {
    this._index = this.length;
    return this;
  }

  // Seeks given number of bytes
  // Note: Backwards seeking is supported
  seek(bytes = 1) {
    this.index += bytes;
    return this;
  }

  // Retrieves number of available bytes
  get available() {
    return this.length - this._index;
  }

  // Reads sequence of given number of bytes (defaults to number of bytes available)
  read(bytes = this.available) {
    if (bytes > this.available) {
      throw new Error(
        "Cannot read " + bytes + " byte(s), " + this.available + " available"
      );
    }

    if (bytes <= 0) {
      throw new RangeError("Invalid number of bytes " + bytes);
    }

    const value = new ByteBuffer(
      this._buffer.slice(this._index, this._index + bytes),
      this.order
    );
    this._index += bytes;
    return value;
  }

  // Writes sequence of bytes
  write(sequence) {
    let view;

    // Ensure we're dealing with a Uint8Array view
    if (!(sequence instanceof Uint8Array)) {
      // Extract the buffer from the sequence
      const buffer = this._extractBuffer(sequence);
      if (!buffer) {
        throw new TypeError("Cannot write " + sequence + ", not a sequence");
      }

      // And create a new Uint8Array view for it
      view = new Uint8Array(buffer);
    } else {
      view = sequence;
    }

    const available = this.available;
    if (view.byteLength > available) {
      if (this._implicitGrowth) {
        this.append(view.byteLength - available);
      } else {
        throw new Error(
          "Cannot write " +
            sequence +
            " using " +
            view.byteLength +
            " byte(s), " +
            this.available +
            " available"
        );
      }
    }

    this._raw.set(view, this._index);
    this._index += view.byteLength;
    return this;
  }

  // Reads UTF-8 encoded string of given number of bytes (defaults to number of bytes available)
  //
  // Based on David Flanagan's BufferView (https://github.com/davidflanagan/BufferView/blob/master/BufferView.js//L195)
  readString(bytes = this.available) {
    if (bytes > this.available) {
      throw new Error(
        "Cannot read " + bytes + " byte(s), " + this.available + " available"
      );
    }

    if (bytes <= 0) {
      throw new RangeError("Invalid number of bytes " + bytes);
    }

    // Local reference
    const raw = this._raw;

    // Holds decoded characters
    const codepoints = [];

    // Index into codepoints
    let c = 0;

    // Bytes
    let b1 = null;
    let b2 = null;
    let b3 = null;
    let b4 = null;

    // Target index
    const target = this._index + bytes;

    while (this._index < target) {
      b1 = raw[this._index];

      if (b1 < 128) {
        // One byte sequence
        codepoints[c++] = b1;
        this._index++;
      } else if (b1 < 194) {
        throw new Error("Unexpected continuation byte");
      } else if (b1 < 224) {
        // Two byte sequence
        b2 = raw[this._index + 1];

        if (b2 < 128 || b2 > 191) {
          throw new Error("Bad continuation byte");
        }

        codepoints[c++] = ((b1 & 0x1f) << 6) + (b2 & 0x3f);

        this._index += 2;
      } else if (b1 < 240) {
        // Three byte sequence
        b2 = raw[this._index + 1];

        if (b2 < 128 || b2 > 191) {
          throw new Error("Bad continuation byte");
        }

        b3 = raw[this._index + 2];

        if (b3 < 128 || b3 > 191) {
          throw new Error("Bad continuation byte");
        }

        codepoints[c++] =
          ((b1 & 0x0f) << 12) + ((b2 & 0x3f) << 6) + (b3 & 0x3f);

        this._index += 3;
      } else if (b1 < 245) {
        // Four byte sequence
        b2 = raw[this._index + 1];

        if (b2 < 128 || b2 > 191) {
          throw new Error("Bad continuation byte");
        }

        b3 = raw[this._index + 2];

        if (b3 < 128 || b3 > 191) {
          throw new Error("Bad continuation byte");
        }

        b4 = raw[this._index + 3];

        if (b4 < 128 || b4 > 191) {
          throw new Error("Bad continuation byte");
        }

        let cp =
          ((b1 & 0x07) << 18) +
          ((b2 & 0x3f) << 12) +
          ((b3 & 0x3f) << 6) +
          (b4 & 0x3f);
        cp -= 0x10000;

        // Turn code point into two surrogate pairs
        codepoints[c++] = 0xd800 + ((cp & 0x0ffc00) >>> 10);
        codepoints[c++] = 0xdc00 + (cp & 0x0003ff);

        this._index += 4;
      } else {
        throw new Error("Illegal byte");
      }
    }

    // Browsers may have hardcoded or implicit limits on the array length when applying a function
    // See: https://developer.mozilla.org/en/JavaScript/Reference/Global_Objects/Function/apply//apply_and_built-in_functions
    const limit = 1 << 16;
    const length = codepoints.length;
    if (length < limit) {
      return String.fromCharCode.apply(String, codepoints);
    }
    const chars = [];
    let i = 0;
    while (i < length) {
      chars.push(
        String.fromCharCode.apply(String, codepoints.slice(i, i + limit))
      );
      i += limit;
    }
    return chars.join("");
  }

  // Writes UTF-8 encoded string
  // Note: Does not write string length or terminator
  //
  // Based on David Flanagan's BufferView (https://github.com/davidflanagan/BufferView/blob/master/BufferView.js//L264)
  writeString(string: string) {
    // Encoded UTF-8 bytes
    const bytes = [];

    // String length, offset and byte offset
    const length = string.length;
    let i = 0;
    let b = 0;

    while (i < length) {
      const c = string.charCodeAt(i);

      if (c <= 0x7f) {
        // One byte sequence
        bytes[b++] = c;
      } else if (c <= 0x7ff) {
        // Two byte sequence
        bytes[b++] = 0xc0 | ((c & 0x7c0) >>> 6);
        bytes[b++] = 0x80 | (c & 0x3f);
      } else if (c <= 0xd7ff || (c >= 0xe000 && c <= 0xffff)) {
        // Three byte sequence
        // Source character is not a UTF-16 surrogate
        bytes[b++] = 0xe0 | ((c & 0xf000) >>> 12);
        bytes[b++] = 0x80 | ((c & 0x0fc0) >>> 6);
        bytes[b++] = 0x80 | (c & 0x3f);
      } else {
        // Four byte sequence
        if (i === length - 1) {
          throw new Error(
            "Unpaired surrogate " + string[i] + " (index " + i + ")"
          );
        }

        // Retrieve surrogate
        const d = string.charCodeAt(++i);
        if (c < 0xd800 || c > 0xdbff || d < 0xdc00 || d > 0xdfff) {
          throw new Error(
            "Unpaired surrogate " + string[i] + " (index " + i + ")"
          );
        }

        const cp = ((c & 0x03ff) << 10) + (d & 0x03ff) + 0x10000;

        bytes[b++] = 0xf0 | ((cp & 0x1c0000) >>> 18);
        bytes[b++] = 0x80 | ((cp & 0x03f000) >>> 12);
        bytes[b++] = 0x80 | ((cp & 0x000fc0) >>> 6);
        bytes[b++] = 0x80 | (cp & 0x3f);
      }

      ++i;
    }

    this.write(bytes);

    return bytes.length;
  }

  // Aliases for reading/writing UTF-8 encoded strings
  // readUTFChars: this.::readString
  // writeUTFChars: this.::writeString

  // Reads UTF-8 encoded C-string (excluding the actual NULL-byte)
  readCString() {
    const bytes = this._raw;
    let length = bytes.length;
    let i = this._index;
    while (bytes[i] !== 0x00 && i < length) {
      ++i;
    }

    length = i - this._index;
    if (length > 0) {
      const string = this.readString(length);
      this.readByte();
      return string;
    }

    return null;
  }

  // Writes UTF-8 encoded C-string (NULL-terminated)
  writeCString(string) {
    let bytes = this.writeString(string);
    this.writeByte(0x00);
    return ++bytes;
  }

  // Prepends given number of bytes
  prepend(bytes) {
    if (bytes <= 0) {
      throw new RangeError("Invalid number of bytes " + bytes);
    }

    const view = new Uint8Array(this.length + bytes);
    view.set(this._raw, bytes);
    this._index += bytes;
    this.buffer = view.buffer;
    return this;
  }

  // Appends given number of bytes
  append(bytes) {
    if (bytes <= 0) {
      throw new RangeError("Invalid number of bytes " + bytes);
    }

    const view = new Uint8Array(this.length + bytes);
    view.set(this._raw, 0);
    this.buffer = view.buffer;
    return this;
  }

  // Clips this buffer
  clip(begin = this._index, end = this.length) {
    if (begin < 0) {
      begin = this.length + begin;
    }
    const buffer = this._buffer.slice(begin, end);
    this._index -= begin;
    this.buffer = buffer;
    return this;
  }

  // Slices this buffer
  slice(begin = 0, end = this.length) {
    const slice = new ByteBuffer(this._buffer.slice(begin, end), this.order);
    return slice;
  }

  // Clones this buffer
  /*
      clone() {
        const clone = new ByteBuffer(this._buffer.slice(0), this.order, this.implicitGrowth);
        clone.index = this._index;
        return clone;
      }*/

  // Reverses this buffer
  reverse() {
    Array.prototype.reverse.call(this._raw);
    this._index = 0;
    return this;
  }

  // Array of bytes in this buffer
  toArray() {
    return Array.prototype.slice.call(this._raw, 0);
  }

  // Short string representation of this buffer
  toString() {
    const order =
      this._order === ByteBuffer.BIG_ENDIAN ? "big-endian" : "little-endian";
    return (
      "[ByteBuffer; Order: " +
      order +
      "; Length: " +
      this.length +
      "; Index: " +
      this._index +
      "; Available: " +
      this.available +
      "]"
    );
  }

  // Hex representation of this buffer with given spacer
  toHex(spacer = " ") {
    return Array.prototype.map
      .call(this._raw, function(byte) {
        return ("00" + byte.toString(16).toUpperCase()).slice(-2);
      })
      .join(spacer);
  }

  // ASCII representation of this buffer with given spacer and optional byte alignment
  toASCII(spacer = " ", align = true, unknown = "\uFFFD") {
    const prefix = align ? " " : "";
    return Array.prototype.map
      .call(this._raw, function(byte) {
        return byte < 0x20 || byte > 0x7e
          ? prefix + unknown
          : prefix + String.fromCharCode(byte);
      })
      .join(spacer);
  }
  // Generic reader
  reader(method, bytes) {
    var b = this;
    return function(order = b._order) {
      if (bytes > b.available) {
        throw new Error(
          "Cannot read " + bytes + " byte(s), " + b.available + " available"
        );
      }

      const value = b._view[method](b._index, order);
      b._index += bytes;
      return value;
    };
  }

  // Generic writer
  writer(method, bytes) {
    var b = this;

    return function(value, order = b._order) {
      const available = b.available;
      if (bytes > available) {
        if (b._implicitGrowth) {
          console.log("growth");
          b.append(bytes - available);
          console.log(b.length);
        } else {
          throw new Error(
            "Cannot write " +
              value +
              " using " +
              bytes +
              " byte(s), " +
              available +
              " available"
          );
        }
      }

      b._view[method](b._index, value, order);
      b._index += bytes;
      return b;
    };
  }
  // Readers for bytes, shorts, integers, floats and doubles

  readByte = this.reader("getInt8", 1);
  readUnsignedByte = this.reader("getUint8", 1);
  readShort = this.reader("getInt16", 2);
  readUnsignedShort = this.reader("getUint16", 2);
  readInt = this.reader("getInt32", 4);
  readUnsignedInt = this.reader("getUint32", 4);
  readFloat = this.reader("getFloat32", 4);
  readDouble = this.reader("getFloat64", 8);

  // Writers for bytes, shorts, integers, floats and doubles
  writeByte = this.writer("setInt8", 1);
  writeUnsignedByte = this.writer("setUint8", 1);
  writeShort = this.writer("setInt16", 2);
  writeUnsignedShort = this.writer("setUint16", 2);
  writeInt = this.writer("setInt32", 4);
  writeUnsignedInt = this.writer("setUint32", 4);
  writeFloat = this.writer("setFloat32", 4);
  writeDouble = this.writer("setFloat64", 8);
}

