import { paintToolManager } from "../painter";
import { BrushBlendType } from "./PaintBrush";
import { Vec } from "../Math/Vector";

export class Color {
  val: number[];
  constructor(r: number, g: number, b: number, a: number) {
    this.val = new Array<number>(4);
    this.val[0] = r;
    this.val[1] = g;
    this.val[2] = b;
    this.val[3] = a;
  }
  clone() {
    return Color.fromArray(this.val);
  }
  static fromArray(array: number[]) {
    return new Color(array[0], array[1], array[2], array[3]);
  }
  get vec4(): Vec {
    var val = this.val;
    return new Vec([val[0], val[1], val[2], val[3]]);
  }
  set r(v: number) {
    this.val[0] = v;
  }
  get r(): number {
    return this.val[0];
  }
  get g(): number {
    return this.val[1];
  }
  get b(): number {
    return this.val[2];
  }
  set a(val: number) {
    this.val[3] = val;
  }
  get a(): number {
    return this.val[3];
  }
  setOpacity(a: number) {
    this.val[0] *= a;
    this.val[1] *= a;
    this.val[2] *= a;
    this.a = a;
  }
  static get red() {
    return new Color(0.1, 0, 0, 0.1);
  }
  static getGrey(level?: number) {
    return new Color(level, level, level, 1);
  }
}
export class ColorPalatte {
  private _currentColor: Color;
  opacity: number = 0.05; //0.1
  get currentColor(): Color {
    if (this._currentColor == null) return null;
    else if (paintToolManager.currentTool.type == BrushBlendType.brush) {
      var c = Color.fromArray(this._currentColor.val);
      c.setOpacity(this.opacity);
      return c;
    } else return Color.getGrey(0.3);
  }
  defaultCurrentColor() {
    this._currentColor = this._palatteColor.clone();
  }
  clearColor() {
    this._currentColor = null;
  }
  _palatteColor: Color;

  setPalatteColor(color: Color) {
    console.log("set palatte");
    this._palatteColor = color;
  }
  get palatteColor() {
    return this._palatteColor;

    //    return new Color(1,1,1,1);
  }
  constructor() {
    this._palatteColor = new Color(0.05, 0.05, 0.05, 0.2);
    this._currentColor = new Color(0.1, 0.1, 0.1, 0.2);
  }
  canvasColorMix(canvasColor: number[]) {
    if (this._currentColor == null) {
      console.log("new");
      this._currentColor = this._palatteColor.clone();
    } else {
      this._currentColor.val = this.blendColor(
        this._palatteColor.val,
        this._currentColor.val,
        canvasColor
      );
      //console.log(color);
    }
  }
  blendColor(ori: number[], current: number[], canvas: number[]) {
    var oriWeight = 0.01;
    var currentWeight = 0.9;
    var canvasWeight = 0.09;

    var cf = new Array(4);
    //var alphaTotal = weight*(c1[3]+c2[3]);
    /*
        for(var i=0;i<4;i++)
        {
          cf[i] = ori[i]*oriWeight;
          cf[i] += current[i]*currentWeight;
          cf[i] += canvas[i]*canvasWeight;
          //cf[i]/=alphaTotal;
        }*/

    for (var i = 0; i < 3; i++) {
      //255 - SQRT(((255-Color1.R)^2 + (255-Color2.R)^2)/2)
      var op2 = 1 - ori[i] / ori[3];
      var cp2 = 1 - canvas[i] / canvas[3];
      var up2 = 1 - current[i] / current[3];
      if (canvas[3] == 0) {
        cp2 = 0;
        oriWeight = 0.1;
      }
      cf[i] =
        1 -
        Math.sqrt(
          op2 * op2 * oriWeight +
            cp2 * cp2 * canvasWeight +
            up2 * up2 * currentWeight
        );
    }
    cf[3] = 1;

    return cf;
  }
}



