import { Input, TextArea, Form } from "semantic-ui-react";
import * as React from "react";
const EditPanel = () =>
  <div>
    標題：<Input />
    描述：<TextArea />
  </div>;

export class EditableText extends React.Component<
  { isEdit: boolean; value: string; dataChanged: (value: string) => void },
  any
> {
  render() {
    return (
      <div>
        {this.props.isEdit
          ? <Input
              placeHolder={"輸入標題"}
              defaultValue={this.props.value}
              onChange={(e, a) => {
                this.props.dataChanged(a.value);
              }}
            />
          : <p>
              {this.props.value}
            </p>}
      </div>
    );
  }
}

//need form tag wrapped to get styling
export class EditableTextArea extends React.Component<
  { isEdit: boolean; value: string; dataChanged: (value: string) => void },
  any
> {
  render() {
    return (
      <div>
        {this.props.isEdit
          ? <TextArea
              rows={4}
              placeHolder={this.props.value}
              defaultValue={this.props.value}
              onChange={(e, a) => {
                this.props.dataChanged(a.value);
              }}
            />
          : <p>
              {this.props.value}
            </p>}
      </div>
    );
  }
}



