import { style } from "glamor";
import { padding } from "glamor/utils";
import * as React from "react";
import { Header, Container, Image, Card, Icon, Menu } from "semantic-ui-react";
import Slider from "react-slick";
import { DashBoardView } from "../User/DashBoardView";
import glamorous from "glamorous";
import { Link } from "react-router-dom";
import { DatabaseTableURL } from "../../engine/Firebase/DatabaseTableURL";
import { MaterialGrid } from "../Material/View/MaterialGrid";
import { MainContainer } from "../index";
import {
  MaterialFilter,
  MaterialOrder,
  materialStore
} from "../store/MaterialStore";
//https://github.com/akiran/react-slick/blob/master/examples/CenterMode.js
var settings = {
  dots: true,
  speed: 500,
  slidesToShow: 3,
  arrows: true,
  autoplay: true,
  centerMode: true,
  autoplaySpeed: 100
};
const featured = new Array();
featured.push("Test");
featured.push("Test1");
featured.push("Tes2");
featured.push("Test1");
featured.push("Test1");

const HomeSlider = () =>
  <Slider {...settings}>
    {featured.map(a => {
      return (
        <div>
          <Link to={DatabaseTableURL.Material + "/"}>
            <Image
              centered
              src="https://react.semantic-ui.com/assets/images/wireframe/image.png"
            />
          </Link>
        </div>
      );
    })}
  </Slider>;
export const Home = () =>
  <MainContainer>
    <Container>
      <HomeSlider />
    </Container>
    <Header textAlign="center">探索新素材</Header>
    <Menu borderless secondary pointing widths={3}>
      <Menu.Item
        onClick={() => {
          materialStore.getMaterialForGrid(
            MaterialFilter.all,
            MaterialOrder.created_at
          );
        }}
        active
      >
        Latest
      </Menu.Item>
      <Menu.Item
        onClick={() => {
          materialStore.getMaterialForGrid(
            MaterialFilter.all,
            MaterialOrder.practiced_count
          );
        }}
      >
        Most Practiced
      </Menu.Item>
      <Menu.Item
        onClick={() => {
          materialStore.getMaterialForGrid(
            MaterialFilter.all,
            MaterialOrder.like_count
          );
        }}
      >
        Most Collected
      </Menu.Item>
    </Menu>
    <MaterialGrid
      filter={MaterialFilter.all}
      order={MaterialOrder.created_at}
    />
  </MainContainer>;



