import { MaterialObject } from "../../../../database/MaterialModel";
import * as React from "react";
import { Link, match, RouteComponentProps } from "react-router-dom";
import {
  Button,
  Icon,
  Form,
  Image,
  Grid,
  Comment,
  Header,
  Sticky,
  Divider
} from "semantic-ui-react";
import { observer } from "mobx-react";
import { RefImageWindow } from "../../component/RefImageWindow";
import { materialStore } from "../../../store/MaterialStore";
import { DatabaseTableURL } from "../../../../engine/Firebase/DatabaseTableURL";
import { observable } from "mobx";
import { EditableText, EditableTextArea } from "./EditMaterialPanel";
import { MaterialGrid } from "../MaterialGrid";
import { MainContainer } from "../../../index";
import { SketchGrid } from "../../../Sketch/SketchGrid";
import { Input } from "semantic-ui-react";
import * as ReactDOM from "react-dom";
import {
  PreviewMaterialGrid,
  HoriScrollArea,
  PreviewImage
} from "../../Create/UploadImage/PreviewMaterialGrid";
const Draggable = require("react-draggable");

interface MaterialPageProps extends RouteComponentProps<any> {}

@observer
export class MaterialPageView extends React.Component<
  MaterialPageProps,
  { contextRef: any }
> {
  state = { contextRef: null };
  @observable mode?: "Edit" | "View" = "View";
  key: string;

  componentWillMount() {
    window.scrollTo(0, 0);
    this.key = this.props.match.params.key;
    materialStore.checkCurrentMaterial(this.key);
    //this.data = tutorialManager.getTutorial(key);
    //firebaseManager.getPracticeFile(this.data.key, this.loadPractice);
  }
  loadPractice(practices_file_paths) {}

  startPractice() {
    console.log("start practice");
  }
  saveMaterialObject() {
    materialStore.currentMaterial;
  }
  handleContextRef = contextRef => this.setState({ contextRef });
  actionContext;
  @observable currentImageIndex: number = 0;

  render() {
    const { contextRef } = this.state;

    return (
      <MainContainer>
        <Grid centered>
          <Grid.Column width="8">
            {materialStore.currentMaterial.detail &&
              <div ref={this.handleContextRef}>
                <Grid.Row>
                  <div
                    style={{
                      height: 400,
                      backgroundColor: "#000",
                      position: "relative"
                    }}
                  >
                    {materialStore.currentMaterial.detail &&
                      <img
                        style={{
                          width: "auto",
                          height: "auto",
                          maxWidth: "100%",
                          maxHeight: "100%",
                          position: "absolute",
                          top: 0,
                          bottom: 0,
                          left: 0,
                          right: 0,
                          margin: "auto"
                        }}
                        src={
                          materialStore.currentMaterial.detail.imgURLs[
                            this.currentImageIndex
                          ]
                        }
                      />}
                  </div>
                  {materialStore.currentMaterial.detail &&
                    materialStore.currentMaterial.detail.imgURLs.length > 1 &&
                    <HoriScrollArea>
                      {materialStore.currentMaterial.detail.imgURLs.map(
                        (image, index) => {
                          return (
                            <PreviewImage
                              src={image}
                              index={index}
                              deletable={false}
                              OnClick={() => (this.currentImageIndex = index)}
                            />
                          );
                        }
                      )}
                    </HoriScrollArea>}
                </Grid.Row>

                <Grid.Row>
                  {/* <Sticky context={contextRef} offset={100}> */}
                  <Link to={DatabaseTableURL.PaintCanvas + "/" + this.key}>
                    <Button fluid color="orange">
                      <Icon name="pencil" />
                      開始練習
                    </Button>
                  </Link>
                  <Button as="label" for="file" color="green">
                    <Icon name="upload" />上傳作品
                    <Input type="file" id="file" style={{ display: "none" }} />
                  </Button>
                  <Button color="blue">
                    <Icon name="bookmark" />收藏
                  </Button>
                  {/* </Sticky> */}
                  <Divider />
                  <Header>
                    參考素材標題：{materialStore.currentMaterial.brief.title}
                  </Header>
                </Grid.Row>

                <Grid.Row>
                  <Divider />
                  <Comment.Group>
                    <Header as="h3">留言？</Header>
                    <Comment>
                      {/* <Comment.Avatar src="https://react.semantic-ui.com/assets/images/avatar/small/matt.jpg" /> */}
                      <Comment.Content>
                        <Comment.Author as="a">Matt</Comment.Author>
                        <Comment.Metadata>
                          <div>Today at 5:42PM</div>
                        </Comment.Metadata>
                        <Comment.Text>How artistic!</Comment.Text>
                        <Comment.Actions>
                          <Comment.Action>Reply</Comment.Action>
                        </Comment.Actions>
                      </Comment.Content>
                    </Comment>
                    <Comment>
                      {/* <Comment.Avatar src="" /> */}
                      <Comment.Content>
                        <Comment.Author as="a">Joe Henderson</Comment.Author>
                        <Comment.Metadata>
                          <div>5 days ago</div>
                        </Comment.Metadata>
                        <Comment.Text>
                          Dude, this is awesome. Thanks so much
                        </Comment.Text>
                        <Comment.Actions>
                          <Comment.Action>Reply</Comment.Action>
                        </Comment.Actions>
                      </Comment.Content>
                    </Comment>

                    <Form reply>
                      <Form.Input placeholder="留言..." />
                    </Form>
                  </Comment.Group>
                </Grid.Row>

                <Grid.Row>
                  <Header>大家的作品</Header>
                  <SketchGrid />
                </Grid.Row>
              </div>}
          </Grid.Column>
        </Grid>
        {/* <Draggable>
          <div className="draggable-element">Drag Me</div>
        </Draggable> */}
        {this.mode == "Edit"
          ? <Button
              onClick={() => {
                this.mode = "View";
                this.saveMaterialObject();
              }}
            >
              完成
            </Button>
          : <Button
              onClick={() => {
                this.mode = "Edit";
              }}
            >
              編輯
            </Button>}
        {materialStore.currentMaterial && materialStore.currentMaterial.detail
          ? <div>
              <Form>
                <EditableText
                  isEdit={this.mode == "Edit"}
                  value={materialStore.currentMaterial.brief.title}
                  dataChanged={value => {
                    materialStore.currentMaterial.brief.title = value;
                  }}
                />
                <EditableTextArea
                  isEdit={this.mode == "Edit"}
                  value={materialStore.currentMaterial.detail.description}
                  dataChanged={value => {
                    materialStore.currentMaterial.detail.description = value;
                  }}
                />
              </Form>
              <img
                style={{ width: "400px" }}
                src={materialStore.currentMaterial.brief.coverImgURL}
                alt=""
              />
              {materialStore.currentMaterial.detail ? <RefImageWindow /> : null}
              <li>
                <Link
                  to={DatabaseTableURL.Sketch + this.props.match.params.key}
                  onClick={this.startPractice}
                >
                  畫！
                </Link>
              </li>
            </div>
          : null}
      </MainContainer>
    );
  }
}

{
  /* style file input
   <Button as="label" for="file" color="green">
<Icon name="upload" />上傳作品
<Input type="file" id="file" style={{ display: "none" }} />
</Button> */
}



