import { createMaterialStore } from "./CreateMaterialStore";
import * as React from "react";
import { Step } from "semantic-ui-react";

export class ProgressStep extends React.Component<any, any> {
  render() {
    return (
      <div>
        <Step.Group>
          <Step
            active={createMaterialStore.status == 0}
            completed={createMaterialStore.status > 0}
            onClick={() => {
              createMaterialStore.status = 0;
            }}
            icon={"image"}
            title="加入素材圖片"
            description="上傳或貼上"
          />
          <Step
            active={createMaterialStore.status == 1}
            completed={createMaterialStore.status > 1}
            onClick={() => (createMaterialStore.status = 1)}
            icon={"compose"}
            title="填寫素材資訊"
            description="主題、描述、類別..."
          />
          <Step
            disabled={createMaterialStore.status != 2}
            icon={"publish"}
            title="發佈"
            description="讓所有人都能練習"
          />
        </Step.Group>
      </div>
    );
  }
}



