import { sketchStore } from "../store/SketchStore";
import * as React from "react";

import { RouteComponentProps, Link } from "react-router-dom";
import { PaintCanvas } from "../PaintTool/PaintCanvas";
import { MainContainer } from "../index";
import {
  Grid,
  Divider,
  Header,
  Icon,
  Button,
  Comment,
  Form,
  Progress
} from "semantic-ui-react";
import { DatabaseTableURL } from "../../engine/Firebase/DatabaseTableURL";
export class SketchPageView extends React.Component<
  RouteComponentProps<any>,
  any
> {
  componentWillMount() {
    this.key = this.props.match.params.key;
    //TODO: get current sketchobject from key
  }
  key: string;
  render() {
    return (
      <MainContainer>
        <Grid centered>
          <Grid.Column width="8">
            <Grid.Row style={{ height: "20em" }}>
              <PaintCanvas />
              <Button
                circular
                style={{ position: "absolute", top: 5, right: 5 }}
                onClick={() => {}}
              >
                <Icon name="maximize" />
              </Button>
              <Progress />
            </Grid.Row>
            <Grid.Row>
              {/* <Sticky context={contextRef} offset={100}> */}
              <Link
                to={
                  DatabaseTableURL.Material +
                  "/" +
                  sketchStore.currentSketch.brief.belong_material_id
                }
              >
                <Button color="orange">
                  <Icon name="image" />
                  回到素材
                </Button>
              </Link>
              <Button color="red">
                <Icon name="heart" />
                {sketchStore.currentSketch.brief.like}
              </Button>
              {/* </Sticky> */}
              <Divider />
              <Header>Title</Header>
            </Grid.Row>

            <Grid.Row>
              <Divider />
              <Comment.Group>
                <Header as="h3">Comments</Header>
                <Comment>
                  {/* <Comment.Avatar src="https://react.semantic-ui.com/assets/images/avatar/small/matt.jpg" /> */}
                  <Comment.Content>
                    <Comment.Author as="a">Matt</Comment.Author>
                    <Comment.Metadata>
                      <div>Today at 5:42PM</div>
                    </Comment.Metadata>
                    <Comment.Text>How artistic!</Comment.Text>
                    <Comment.Actions>
                      <Comment.Action>Reply</Comment.Action>
                    </Comment.Actions>
                  </Comment.Content>
                </Comment>
                <Comment>
                  {/* <Comment.Avatar src="" /> */}
                  <Comment.Content>
                    <Comment.Author as="a">Joe Henderson</Comment.Author>
                    <Comment.Metadata>
                      <div>5 days ago</div>
                    </Comment.Metadata>
                    <Comment.Text>
                      Dude, this is awesome. Thanks so much
                    </Comment.Text>
                    <Comment.Actions>
                      <Comment.Action>Reply</Comment.Action>
                    </Comment.Actions>
                  </Comment.Content>
                </Comment>

                <Form reply>
                  <Form.Input placeholder="留言..." />
                </Form>
              </Comment.Group>
            </Grid.Row>
          </Grid.Column>
        </Grid>
      </MainContainer>
    );
  }
}



