import { SketchObject } from "../../database/SketchModel";
import { DataModel, DataObject } from "../../database/DataModel";
import {
  MaterialBriefModel,
  MaterialObject,
  MaterialDetailModel
} from "../../database/MaterialModel";
import { DatabaseTableURL } from "../../engine/Firebase/DatabaseTableURL";
import { observable, toJS } from "mobx";
import { CommentForm } from "../PaintTool/CommentProcess/CommentForm";

//把重要的事做了，其他就不重要
//技術作為服務
//產品

class SketchStore {
  @observable isReady = false;
  sketches: SketchObject[] = [];
  currentSketch: SketchObject = new SketchObject();
  getSketchFromMaterial() {}
  fakeSketches() {
    this.isReady = true;
    this.sketches = new Array<SketchObject>();
    for (var i = 0; i < 10; i++) this.sketches.push(new SketchObject());
    return this.sketches;
  }
  //user_like_sketch_table
}

export var sketchStore = new SketchStore();



