import { position } from "glamor/utils";
import { PublishMaterialForm } from "./PublishMaterialForm";
import { createMaterialStore } from "./CreateMaterialStore";
import * as React from "react";
import { Link, Router, RouteComponentProps, match } from "react-router-dom";
import {
  Menu,
  Button,
  Modal,
  Header,
  Image,
  Icon,
  Tab,
  Input,
  Popup,
  Grid,
  Step,
  Item
} from "semantic-ui-react";
import { observable } from "mobx";
import { observer } from "mobx-react";
import { MaterialURLInput } from "./UploadImage/MaterialURLInput";
import { MaterialUploadInput } from "./UploadImage/MaterialUploadInput";
import glamorous from "glamorous";
import { UploadOrLink } from "./UploadImage/UploadOrLink";
import { MaterialDetailForm } from "./UploadImage/MaterialDetailForm";
import { PreviewMaterialGrid } from "./UploadImage/PreviewMaterialGrid";
import { ProgressStep } from "./CreateProgressStep";
import { userStore } from "../../store/UserStore";

const steps = [
  { active: true, icon: "image", title: "搜集", description: "上傳或貼上" },
  { icon: "text", title: "描述", description: "Enter billing information" },
  { disabled: true, icon: "info", title: "Confirm Order" }
];

@observer
export class CreateMaterialModal extends React.Component<
  { trigger: any; onClose: Function },
  any
> {
  handleClose() {
    this.props.onClose();

    createMaterialStore.closeModal();
    console.log("close modal");
  }
  handleOpen() {
    createMaterialStore.isModalOpen = true;
  }
  render() {
    return (
      <Modal
        size={"small"}
        trigger={this.props.trigger}
        onOpen={() => {
          this.handleOpen();
        }}
        closeOnDimmerClick={false}
        closeOnPortalMouseLeave={false}
        dimmer="blurring"
        open={createMaterialStore.isModalOpen}
      >
        <Modal.Header>
          <Menu secondary>
            <Menu.Item>建立素材包</Menu.Item>

            <Menu.Item
              className="right"
              icon="close"
              onClick={() => {
                this.handleClose();
              }}
            >
              <Icon name="close" />
            </Menu.Item>
          </Menu>
        </Modal.Header>
        <Modal.Actions>
          <ProgressStep />
        </Modal.Actions>
        <Modal.Content>
          <Modal.Description>
            {(() => {
              switch (createMaterialStore.status) {
                case 0:
                  return (
                    <div>
                      <PreviewMaterialGrid deleteable={true} />
                      <UploadOrLink />
                    </div>
                  );
                case 1:
                  return (
                    <div>
                      <PreviewMaterialGrid deleteable={false} />
                      <MaterialDetailForm />
                    </div>
                  );
                case 2:
                  return (
                    <div>
                      <PreviewMaterialGrid deleteable={false} />
                      <PublishMaterialForm />
                    </div>
                  );
              }
            })()}
            {}
          </Modal.Description>
          {createMaterialStore.status < 2
            ? <Menu secondary>
                <Menu.Item position="right">
                  <Button
                    onClick={() => {
                      if (createMaterialStore.status == 1) {
                        //upload
                        if (userStore.currentUser) {
                          createMaterialStore.createMateiral().then(a => {
                            createMaterialStore.status++;
                          });
                        } else {
                        }
                      } else createMaterialStore.status++;
                    }}
                  >
                    下一步
                  </Button>
                </Menu.Item>
              </Menu>
            : null}
        </Modal.Content>
      </Modal>
    );
  }
}



