import { DataModel, DataObject } from "./DataModel";
export class SketchObject extends DataObject {
  constructor() {
    super();
    this.brief = new SketchBriefModel();
  }
  brief: SketchBriefModel;
  detail: SketchDetailModel;
}
export class SketchBriefModel extends DataModel {
  uid: string;
  belong_material_id: string;
  title: string = "SketchTitle";
  like: number = 0;
}

export class SketchDetailModel extends DataModel {
  filePath: string;
}



