import { SAFile } from "../painter/artwork/SAFile";
import { dialog, is_web_browser, is_electron, path } from "../painter/painter";
import { SAArchiver } from "../painter/artwork/SAArchiver";
import SAArtwork from "../painter/artwork/SAArtwork";
import { SAUnArchiver } from "../painter/artwork/SAUnarchiver";

export class FileManager {
  currentFile: SAFile;
  constructor() {
    this.newFile(1024, 768);
  }
  newFile(width: number, height: number) {
    //TODO: warn save file
    this.currentFile = new SAFile("Jerry", new SAArtwork(width, height));
  }
  loadCloudFile() {}
  saveFile() {
    var fileString = SAArchiver.serialize(this.currentFile);
    if (is_electron()) {
      this.saveFileLocal(fileString, this.currentFile);
    }
    this.saveFileCloud(this.currentFile);
  }

  //TODO:
  saveFileCloud(file: SAFile) {
    //firebaseManager.savePracticeFile(file)
  }

  loadFileLocal(dirName = null) {
    var fm = this;
    if (dirName == null) {
      dialog.showOpenDialog({ properties: ["openFile"] }, function(
        fileName: string
      ) {
        console.log(fileName[0]);
        fm.readFile(fileName[0]);
      });
    } else {
      this.readFile(dirName);
    }
  }

  saveFileLocal(fileContent: string, file: SAFile) {
    var fm = this;
    if (file.__path == null) {
      dialog.showSaveDialog(
        { properties: ["saveFile"], fileter: [".sa"] },
        function(filePath: string) {
          fm.writeFile(fileContent, filePath);
          fm.currentFile.__filename = path.basename(filePath);
          fm.currentFile.__path = filePath;
        }
      );
    } else {
      console.log("save:" + file.__path);
      this.writeFile(fileContent, file.__path);
    }
  }
  writeFile(fileContent: string, filePath: string) {
    var fs = window.require("fs");
    try {
      fs.writeFileSync(filePath, fileContent, "utf-8");
    } catch (e) {
      alert("Failed to save the file !");
    }
  }
  readFile(filePath: string) {
    console.log("!!load:" + filePath);
    //require node only for electron, webpack ignored
    var fs = window.require("fs");
    var fm = this;
    var data = fs.readFileSync(filePath);
    var jobj = JSON.parse(data);
    var ua = new SAUnArchiver();
    fm.currentFile = ua.parseSAFile(data);
  }
}



