import { SAFile } from './SAFile';
import ByteBuffer from '../../bytebuffer';
export class SAArchiver {
    constructor() {
        
    }
    static serialize(file:SAFile)
    {
        var bufferLength = file.artwork.dataSize()
        console.log(bufferLength)
        var byteBuffer = new ByteBuffer(bufferLength)
        file.artwork.writeToData(byteBuffer)
        //var f16 = Float64Array.from(file.artwork.exportArray());
        //var content = this.arrayBufferToBase64(f16.buffer);

         var content = this.arrayBufferToBase64(byteBuffer.buffer);
         var jobj = new Object as any
         jobj.path = file.__path
         jobj.author = file.author
         jobj.content = content
         
         return JSON.stringify(jobj)
    }
    
    static arrayBufferToBase64(buffer:ArrayBuffer) {
        var binary = '';
        var bytes = new Uint8Array( buffer );
        var len = bytes.byteLength;
        for (var i = 0; i < len; i++) {
            binary += String.fromCharCode( bytes[ i ] );
        }
        return btoa( binary );
    }
}
    


