import { SketchObject } from "../../database/SketchModel";
import * as React from "react";
import { Card, Icon, Image } from "semantic-ui-react";
import { DatabaseTableURL } from "../../engine/Firebase/DatabaseTableURL";
import { sketchStore } from "../store/SketchStore";

export class SketchCell extends React.Component<{ data: SketchObject }, any> {
  data: SketchObject;
  componentWillMount() {
    this.data = this.props.data;
  }
  // meta=""
  // description=""

  render() {
    var height = (100 + Math.random() * 50).toFixed(0);
    return (
      <Card
        onClick={() => {
          sketchStore.currentSketch = this.data;
        }}
        //fake image
        href={"#" + DatabaseTableURL.Sketch + "/" + this.data.key}
        image={
          <Image
            src={"http://placehold.it/100x" + height}
            style={{ height: height }}
          />
        }
        header={this.data.brief.title}
        extra={
          <div>
            <Icon name="heart" as="a" onClick={() => {}} />
            {this.data.brief.like}
          </div>
        }
      />
    );
  }
}



