import { GLContextBuffer } from "./GLContextBuffer";
import { fileManager, paintToolManager } from "../painter";
import GLStrokeRenderer from "./GLStrokeRenderer";
import {
  UserCommand,
  UserCommandType,
  ColorCommandData,
  LayerCommandType,
  LayerCommandData
} from "../event/UserCommand";
import PaintStroke from "../stroke/PaintStroke";
import SAClip from "../artwork/SAClip";
import { PointData } from "../stroke/PaintPoint";
import { ToolCommandData } from "../event/UserCommand";
import { PaintBrush } from "../PaintTool/PaintBrush";
export class PaintReplayer {
  renderer: GLStrokeRenderer;
  get clip() {
    return fileManager.currentFile.artwork.masterClip;
  }
  contextBuffer: GLContextBuffer;
  constructor(context: GLContextBuffer) {
    this.contextBuffer = context;
    this.renderer = context.strokeRenderer;
  }

  drawClip(clip: SAClip) {}

  drawToProgressPercent(percent: number) {
    var commands = this.clip.fullCommands;
    if (commands.length < 1) return;
    var at = Math.round(percent * commands.length);
    //console.log(""+at+", "+this.commands.length+", "+percent)
    var replayer = this;
    if (!this.isDrawing)
      setTimeout(function() {
        replayer.drawToProgress(at);
      }, 0);
  }
  isDrawing: boolean = false;
  drawToProgress(to: number) {
    if (this.isDrawing == true) return;
    this.isDrawing = true;
    var commands = this.clip.fullCommands;

    if (to == 0) {
      this.contextBuffer.clearCanvas();
    } else if (to == this.clip.currentCommandIndex) {
      this.isDrawing = false;
      return;
    } else if (to > this.clip.currentCommandIndex) {
      //< 畫後面的部分
      console.log("draw forward");
      for (var i = this.clip.currentCommandIndex; i < to; i++) {
        if (commands[i].undoIndex == 0 || commands[i].undoIndex > to)
          this.excuteCommand(commands[i]);
      }
    } else {
      //重頭畫到目前的指令
      console.log("draw backward");
      this.contextBuffer.clearCanvas();
      for (var i = 0; i < to; i++) {
        if (commands[i].undoIndex == 0 || commands[i].undoIndex > to)
          this.excuteCommand(commands[i]);
      }
    }
    this.clip.currentCommandIndex = to;
    GLContextBuffer.isDirty = true;
    this.contextBuffer.renderCanvas();
    this.isDrawing = false;
  }
  checkUndo() {
    var commands = this.clip.fullCommands;
    var to = commands.length;
    for (var i = 0; i < to; i++) {
      if (commands[i].undoIndex == 0 || commands[i].undoIndex > to) {
        this.excuteCommand(commands[i]);
        console.log("cmd");
      } else {
        console.log("undo");
      }
    }
  }
  drawAll() {
    this.contextBuffer.clearCanvas();
    var commands = this.clip.fullCommands;
    var to = commands.length;
    for (var i = 0; i < to; i++) {
      this.excuteCommand(commands[i]);
      // if(commands[i].undoIndex==0||commands[i].undoIndex>to)
      // {
      //     this.excuteCommand(commands[i]);
      //     console.log("cmd");
      // }
      // else
      // {
      //     console.log("undo");
      // }
    }
    this.clip.currentCommandIndex = commands.length;
    GLContextBuffer.isDirty = true;
    //this.contextBuffer.renderCanvas();
    this.isDrawing = false;
  }

  excuteCommand(event: UserCommand) {
    console.log(event);
    switch (event.type) {
      case UserCommandType.stroke:
        var stroke = event.commandData as PaintStroke;
        var tool = paintToolManager.userTool[event.user];
        this.renderer.drawPoints(stroke.points, tool);
        break;
      case UserCommandType.tool:
        var tcmd = event.commandData as ToolCommandData;
        paintToolManager.excuteCommand(event);
        console.log("Tool:", tcmd.type);
        break;
      case UserCommandType.tool_attribute:
        break;
      case UserCommandType.zoom:
        break;
      case UserCommandType.layer:
        break;
      case UserCommandType.color:
        var cmd = event.commandData as ColorCommandData;
        //colorPalatte.palatteColor = Color.fromArray(cmd.val)
        break;
      case UserCommandType.undo:
        //this.drawToProgress(this.clip.currentCommandIndex);
        //this.drawAll();
        break;
    }
  }
  timer: number;
  lastTimeStamp: number;
  //currentCommandIndex:number = 0
  currentStroke: PaintStroke;
  currentPointIndex: number = 0;
  isPause: boolean;
  restartReplay() {
    this.clip.currentCommandIndex = 0;
    this.currentPointIndex = 0;
    //this.replayCommand();
  }
  lastPoint: PointData;

  //TODO: realtime replay (video)
  // replayCommand() {
  //   if (this.currentPointIndex == 0) {
  //     this.findNextStroke();
  //     this.lastPoint = this.currentStroke.points[this.currentPointIndex];
  //   }
  //   var player = this;

  //   var points = new Array<PointData>(
  //     this.lastPoint,
  //     this.currentStroke.points[this.currentPointIndex]
  //   );
  //   this.lastPoint = this.currentStroke.points[this.currentPointIndex];
  //   var t = this.currentStroke.points[this.currentPointIndex].timestamp;
  //   var diff = t - this.lastTimeStamp;
  //   this.lastTimeStamp = t;
  //   this.currentPointIndex++;
  //   while (this.currentPointIndex < this.currentStroke.points.length) {
  //     if (this.currentStroke.points[this.currentPointIndex].timestamp == t)
  //       points.push(this.currentStroke.points[this.currentPointIndex++]);
  //     else break;
  //   }
  //   if (this.currentPointIndex == this.currentStroke.points.length) {
  //     this.currentPointIndex = 0;
  //     this.clip.currentCommandIndex++;

  //     if (this.clip.fullCommands.length == this.clip.currentCommandIndex) {
  //       //all commands done
  //       return;
  //     }
  //   }
  //   this.renderer.drawPoints(points, );
  //   this.contextBuffer.renderCanvas();
  //   setTimeout(function() {
  //     player.replayCommand();
  //   }, diff);
  // }
  findNextStroke() {
    var cmd = this.clip.fullCommands[this.clip.currentCommandIndex];
    switch (cmd.type) {
      case UserCommandType.stroke:
        this.currentStroke = cmd.commandData as PaintStroke;
        this.lastTimeStamp = cmd.timestamp;
        break;
      default:
        this.excuteCommand(cmd);
        this.clip.currentCommandIndex++;
        this.findNextStroke();
        break;
    }
  }
  excuteLayerCommand(command: LayerCommandData) {
    switch (command.type) {
      case LayerCommandType.add:
        this.contextBuffer.addLayer();
        break;
      case LayerCommandType.delete:
        this.contextBuffer.deleteLayer(command.index);
        break;
      case LayerCommandType.move:
        this.contextBuffer.moveLayer(command.index, command.to);
        break;
    }
  }

  excuteDrawingCommands(commands: UserCommand[]) {
    for (var i = 0; i < commands.length; i++) {
      var cmd = commands[i];
      if (cmd.type != UserCommandType.zoom) {
        this.excuteCommand(cmd);
      }
    }
    this.contextBuffer.renderCanvas();
  }
}



