import * as React from "react";
import { MaterialRefView } from "./MaterialRefView";
import {
  initCanvas,
  loadPainter,
  mouseEventHandler
} from "../../engine/painter/painter";
import { RouteComponentProps } from "react-router-dom";
import { materialStore } from "../store/MaterialStore";
import { webViewStore } from "../store/WebViewStore";
import { observer } from "mobx-react";
import * as ReactDOM from "react-dom";
import { style } from "glamor";

//TODO:
//View mode and Draw Mode
export interface PaintCanvasProps extends RouteComponentProps<any> {}
@observer
export class PaintCanvas extends React.Component<any, any> {
  componentWillMount() 
  {
    var gl = GL.create({antialias: false,preserveDrawingBuffer: true,premultipledAlpha:true});
  }
  componentDidMount() {
    //gl.canvas.style.margin = "auto";
    gl.canvas.style.position = "absolute";
    gl.canvas.style.top = "0";
    gl.canvas.style.left = "0";
    gl.canvas.style.right = "0";
    gl.canvas.style.bottom = "0";
    
    gl.canvas.style.margin = "auto"
    ReactDOM.findDOMNode(this).appendChild(gl.canvas);
    loadPainter();
    //gl.canvas.style.transform =
    initCanvas();
  }
  componentWillUnmount() {}
  render() {
    return (
      <div
        canvas-area
        style={{
          width: "100%",
          height: "100%",
          position: "relative",
          backgroundColor: "#000"
        }}
      />
    );
  }
}



