import {
  is_web_browser,
  ipcRenderer,
  sc_h,
  sc_w,
  paintRecorder,
  colorPalatte
} from "../painter/painter";
import { GLContextBuffer } from "../painter/render/GLContextBuffer";
import { PointData } from "../painter/stroke/PaintPoint";
import { Vec } from "../painter/Math/Vector";
import {
  glContextBuffer,
  paintToolManager,
  paintRecorder
} from "../painter/painter";
import { userStore } from "../../components/store/UserStore";
export class MouseEventHandler {
  isMouseDown = false;
  glContextBuffer: GLContextBuffer;
  lastPos: GL.Vector;
  lastPoint: PointData;
  cursorPoint: PointData;
  offsetx: number;
  offsety: number;
  win_screen_offset: number[];
  scrollOffset: number = 1;

  disableScroll() {
    function preventDefault(e: Event) {
      e = e || window.event;
      if (e.preventDefault) e.preventDefault();

      e.returnValue = false;
    }
    window.onwheel = preventDefault; // modern standard
    //window.removeEventListener('onwheel')
    //window.onmousewheel = document.onmousewheel = preventDefault; // older browsers, IE
    window.ontouchmove = preventDefault; // mobile
  }
  enableScroll() {
    window.onwheel = null;
    window.ontouchmove = null;
  }
  painterInit() {
    this.disableScroll();
  }
  painterExit() {
    this.enableScroll();
  }

  constructor(glContextBuffer: GLContextBuffer) {
    //this.disableScroll();
    this.glContextBuffer = glContextBuffer;
    var width = glContextBuffer.width;
    var height = glContextBuffer.height;

    var offset = gl.canvas.getBoundingClientRect();
    this.offsetx = -offset.left;
    this.offsety = -offset.top;
    // console.log(this.offsetx);
    // console.log(this.offsety);
    if (!is_web_browser) {
      var screenOffset = ipcRenderer.sendSync("getOffset", "offset");
      this.offsetx += screenOffset[0];
      this.offsety += screenOffset[1];
    }

    gl.canvas.parentElement.addEventListener("mousedown", e => {
      var tablet = false;
      this.isMouseDown = true;
      var x, y;
      var force = 1;
      if (!is_web_browser) {
        var mouseData = ipcRenderer.sendSync("mousedown", "mouse");

        if (mouseData != null) {
          x = mouseData.x + this.offsetx;
          y = sc_h - mouseData.y + this.offsety;
          force = mouseData.force;
          tablet = true;
        }
      }

      if (!tablet) {
        x = e.offsetX;
        y = sc_h - e.offsetY;
        force = 1;
      }

      var pos = this.screenSpaceToCanvasSpace(x, y);
      //this.readColorAndMix(pos)
      this.lastPos = pos;
      this.lastScreenPoint = this.screenSpaceToWorldSpace(x, y);

      //currentColor = palatteColor;

      //TODO:
      this.lastPoint = new PointData(
        new Vec([pos.x, pos.y, 0, 1]),
        new Vec([0, 0]),
        force,
        1
      ); //colorPalatte.palatteColor.vec4
      paintRecorder.userStartDraw(userStore.myUserID, this.lastPoint);
    });

    gl.canvas.parentElement.addEventListener("mousemove", e => {
      var tablet = false;
      var force = 1;
      if (!is_web_browser) {
        //get mouse data from electron
        var mouseData = ipcRenderer.sendSync("mousemove", "mouse");
        if (mouseData != null) {
          console.log(mouseData);
          tablet = true;
          for (var i = 0; i < mouseData.length; i++) {
            var x, y;
            x = mouseData[i].x + this.offsetx;
            y = sc_h - mouseData[i].y + this.offsety;
            force = mouseData[i].force;
            console.log(mouseData[i].x);
            console.log(this.offsetx);

            var azi_x = mouseData[i].azi_x;
            var azi_y = mouseData[i].azi_y;
            var alt = mouseData[i].altitude;
            if (this.isMouseDown) {
              this.movePoint(x, y, force, azi_x, azi_y, alt);
            }
          }
        }
      }

      if (tablet == false) {
        x = e.offsetX;
        y = sc_h - e.offsetY;

        if (e.button == 2) {
          //right button
          //glContextBuffer.zoom()
        } else {
          // var rect = gl.canvas.getBoundingClientRect();
          // x = e.pageX - rect.left;
          // console.log(rect.left);

          // y = sc_h - (e.pageY - rect.top);
          // console.log();
          this.lastScreenPoint = this.screenSpaceToWorldSpace(x, y);
          if (e.target == gl.canvas)
            this.movePoint(x, y, force, 0, 0, Math.PI / 2);
          else {
            this.lastPoint = null;
            this.lastPos = null;
          }
        }
      }
    });

    gl.canvas.parentElement.addEventListener("mouseup", e => {
      this.isMouseDown = false;
      this.lastPoint = null;
      colorPalatte.clearColor();
      paintRecorder.userDrawDone(userStore.myUserID);
    });
    // gl.canvas.parentElement.addEventListener("mousewheel", function(e) {
    //   if (e.ctrlKey) {
    //     e.preventDefault();
    //   }
    // });

    gl.canvas.parentElement.addEventListener("mousewheel", e => {
      var x = e.offsetX;
      var y = sc_h - e.offsetY;
      //console.log(e.deltaY)
      var scale = 1;
      if (e.ctrlKey) {
        //pinch
        e.preventDefault();
        e.stopImmediatePropagation();

        var z = 1 - (e.deltaX + e.deltaY) / 100;

        var px = e.offsetX / gl.canvas.width;
        var py = e.offsetY / gl.canvas.height;
        console.log(px);
        console.log(py);
        glContextBuffer.zoom(z, px, py);
        //glContextBuffer.zoom(e.deltaX,0,0)
      } else {
        glContextBuffer.pan(e.deltaX, e.deltaY);
        var v = this.screenSpaceToWorldSpace(x, y);
      }

      //glContextBuffer.zoom(scale, v.x, v.y);
    });
  }

  screenSpaceToCanvasSpace(x: number, y: number) {
    ///window space to canvas space
    var glContextBuffer = this.glContextBuffer;
    var pos = new GL.Vector(x * 2 / sc_w - 1, y * 2 / sc_h - 1);
    //var pos = new GL.Vector(x/sc_w,(y/sc_h));
    pos = glContextBuffer.renderMVP.inverse().transformPoint(pos);
    pos.x = (pos.x + 1) * glContextBuffer.width / 2;
    pos.y = (pos.y + 1) * glContextBuffer.height / 2;
    //pos.x = pos.x*glContextBuffer.width;
    //pos.y = pos.y*glContextBuffer.height;
    return pos;
  }
  screenSpaceToWorldSpace(x: number, y: number) {
    var pos = new GL.Vector(x * 2 / sc_w - 1, y * 2 / sc_h - 1);
    return pos;
  }
  readColorAndMix(pos: GL.Vector) {
    var glContextBuffer = this.glContextBuffer;
    var canvasColor = glContextBuffer.readPixel(pos.x, pos.y);
    if (
      pos.x < 0 ||
      pos.x > glContextBuffer.width ||
      pos.y < 0 ||
      pos.y > glContextBuffer.height
    ) {
      if (colorPalatte.currentColor == null) {
        colorPalatte.defaultCurrentColor();
      }
    } else colorPalatte.canvasColorMix(canvasColor);
  }
  lastScreenPoint: GL.Vector;
  panCanvas(x: number, y: number) {
    var glContextBuffer = this.glContextBuffer;
    var pos = this.screenSpaceToWorldSpace(x, y);

    if (this.lastScreenPoint != null) {
      var offset = pos.subtract(this.lastScreenPoint);
      glContextBuffer.canvasPosX += offset.x;
      glContextBuffer.canvasPosY += offset.y;
      this.lastScreenPoint = pos;
      GLContextBuffer.isDirty = true;
      console.log("pan");
    }
  }
  movePoint(
    x: number,
    y: number,
    force: number,
    azi_x: number,
    azi_y: number,
    altitude: number
  ) {
    var glContextBuffer = this.glContextBuffer;
    var pos = this.screenSpaceToCanvasSpace(x, y);
    //this.readColorAndMix(pos)
    var point = new PointData(
      new Vec([pos.x, pos.y, 0, 1]),
      new Vec([azi_x, azi_y]),
      force,
      altitude
    );
    this.cursorPoint = point;
    glContextBuffer.drawCursor();
    if (this.lastPoint != null) {
      var newDir = pos.subtract(this.lastPos);
      //drawStroke(pos)

      //var lastPoint = new PaintPoint(lastPos,force,dir);

      //TODO:don't draw firstPoint! d:lastPointd:, point inclusive
      if (this.isMouseDown) {
        if (
          glContextBuffer.strokeRenderer.drawPoints(
            [this.lastPoint, point],
            paintToolManager.myTool
          )
        ) {
          //glContextBuffer.renderCanvas();
          //renderScene();
          this.lastPos = pos;
          this.lastPoint = point;
          paintRecorder.userDrawMove(userStore.myUserID, [point]);
        }
      }
    } else {
      //TODO:temp, mouse leave event of canvas
      this.lastPos = pos;
      this.lastPoint = point;
    }
  }
}



