import { createMaterialStore } from "../CreateMaterialStore";
import { materialStore } from "../../../store/MaterialStore";
import * as React from "react";
import { Input, Container, TextArea, Form } from "semantic-ui-react";

export class MaterialDetailForm extends React.Component<any, any> {
  handleChange = (e, { name, value }) => {
    console.log(name);
    console.log(value);
    createMaterialStore[name] = value;
  };
  render() {
    return (
      <div>
        <Form>
          <Form.Group widths="equal">
            <Form.Input
              name="title"
              onChange={this.handleChange}
              label="素材名稱"
              placeholder="這個素材叫做..."
            />
          </Form.Group>

          <Form.TextArea
            name="description"
            onChange={this.handleChange}
            label="素材描述"
            placeholder="關於這個素材..."
          />
        </Form>
      </div>
    );
  }
}



