export class Layer
{
    opacity:number
    texture:GL.Texture
    isHidden:Boolean = false
    constructor(width:number,height:number)
    {
        this.texture = new GL.Texture(width,height);
        this.opacity = 1
    }
    setTexure(t:GL.Texture)
    {
        this.texture = t;
    }
}


