import { observer } from "mobx-react";
import { observable } from "mobx";
import * as React from "react";
import { Modal, Menu, Header, Button } from "semantic-ui-react";
import { firebaseManager } from "../index";

@observer
export class LoginModal extends React.Component<
  { triggerComponent: JSX.Element; info: string },
  any
> {
  @observable modelOpen;
  render() {
    return (
      <Modal
        size={"small"}
        trigger={this.props.triggerComponent}
        open={this.modelOpen}
      >
        <Modal.Header>登入</Modal.Header>

        <Modal.Content>
          <Modal.Description>
            <Header>
              {this.props.info}
            </Header>
            <Button
              onClick={() => {
                firebaseManager.loginManager.anoymousLogin().then(() => {
                  this.modelOpen = false;
                  window.location.reload();
                });
              }}
            >
              匿名登入
            </Button>
            <Button
              color={"blue"}
              onClick={() => {
                firebaseManager.loginManager.fbLogin().then(() => {
                  this.modelOpen = false;
                  window.location.reload();
                });
              }}
            >
              {firebaseManager.loginManager.user != null ? "已登入" : "FB登入"}
            </Button>
          </Modal.Description>
        </Modal.Content>
      </Modal>
    );
  }
}



