import ByteBuffer from "../../bytebuffer";
import { Vec } from "../Math/Vector";
import { PaintBrush } from "../PaintTool/PaintBrush";

/*PaintPoint: 
  the points to send to shader
*/
export class PaintPoint {
  //UInt16*4
  position: Vec; //4
  //Float32*2 8
  velocity: Vec; //2
  //Float32*2 8
  azimuth: Vec; //2
  //Float32 4
  force: number;
  //Float32 4
  altitude: number;
  color: Vec; //4, no need to save in data
  public static one() {
    //return new PaintPoint(new Vec4(1,1,1,1),0.5,0.5,new Vec2(0,0),new Vec2(1,1));
  }

  static parseFromArray(val: number[], ptr: number) {
    var p = new PaintPoint();
    p.position = new Vec(val.slice((ptr += 4), 4));
    p.velocity = new Vec(val.slice((ptr += 2), 2));
    p.azimuth = new Vec(val.slice((ptr += 2), 2));
    p.force = val[ptr++];
    p.altitude = val[ptr++];
    return p;
  }

  //return only
  public static interpolate(
    p1: PaintPoint,
    p2: PaintPoint,
    pointsBuffer: PaintPointArray,
    brush: PaintBrush
  ) {
    //TODO - size should be dinamic
    var kBrushPixelStep = brush.size * brush.dis;
    var dis = p1.position.subtract(p2.position).length();
    var pnum = Math.ceil(dis / kBrushPixelStep);
    if (pnum <= 1) return false;

    var count = pnum;
    if (p1.velocity == null) {
      p1.velocity = p2.velocity;
    }
    for (var i = 1; i <= count; i += 1) {
      var t = i / count;
      var arc = 0.5 - Math.abs(t - 0.5);
      //pos.x+=arc*vel.x;
      //pos.y+=arc*vel.y;

      /*var color = new Array(4);
            for(var j=0;j<4;j++)
            {
                color[j] = p1.color[j]+t*(p2.color[j]-p1.color[j]);
            }*/
      var pos = Vec.lerp(p1.position, p2.position, t);
      var vel = Vec.lerp(p1.velocity, p2.velocity, t);
      var color = Vec.lerp(p1.color, p2.color, t);
      var azimuth = Vec.lerp(p1.azimuth, p2.azimuth, t);
      pointsBuffer.addForce(p1.force + t * (p2.force - p1.force));
      pointsBuffer.addPosition(pos);
      pointsBuffer.addColors(color);
      pointsBuffer.addVelocity(vel);
      pointsBuffer.addAzimuth(azimuth);
      var alt = p1.altitude + t * (p2.altitude - p1.altitude);
      pointsBuffer.addAltitude(alt);
    }
    return true;
  }
  constructor(
    position?: Vec,
    force?: number,
    altitude?: number,
    azimuth?: Vec,
    velocity?: Vec,
    color?: Vec
  ) {
    this.position = position;
    this.force = force;
    this.altitude = altitude;
    this.azimuth = azimuth;
    this.velocity = velocity;
    this.color = color;
  }
}
export class PaintPointArray {
  positions: number[][];
  forces: number[];
  colors: number[][];
  velocities: number[][];
  altitude: number[];
  azimuth: number[][];
  constructor() {
    this.positions = new Array<Array<number>>();
    this.forces = new Array<number>();
    this.colors = new Array<Array<number>>();
    this.velocities = new Array<Array<number>>();
    this.altitude = new Array<number>();
    this.azimuth = new Array<Array<number>>();
  }
  addPointData(p: PointData) {
    this.addAltitude(p.altitude);
    this.addAzimuth(p.azimuth);
    this.addColors(new Vec([0.5, 0.5, 0.5, 0.5]));
    this.addForce(p.force);
    this.addPosition(p.position);
    this.addVelocity(new Vec([0, 0]));
  }
  addPosition(pos: Vec) {
    this.positions.push(pos.toArray);
  }
  addForce(f: number) {
    this.forces.push(f);
  }
  addColors(color: Vec) {
    this.colors.push(color.toArray);
  }
  addVelocity(vel: Vec) {
    this.velocities.push(vel.toArray);
  }
  addAltitude(f: number) {
    this.altitude.push(f);
  }
  addAzimuth(vec: Vec) {
    this.azimuth.push(vec.toArray);
  }
}
export class PointData {
  position: Vec; //4
  azimuth: Vec; //2
  force: number;
  altitude: number;
  color: Vec; //4
  timestamp: number;
  constructor(
    position?: Vec,
    azimuth?: Vec,
    force?: number,
    altitude?: number
  ) {
    this.position = position;
    this.azimuth = azimuth;
    this.force = force;
    this.altitude = altitude;
    //this.color = color
    this.timestamp = Date.now();
  }
  //timestamp
  //position
  //azimuth
  //force
  //altitude
  //color
  get exportArray() {
    var array = new Array<number>().concat(
      this.timestamp,
      this.position.toArray,
      this.azimuth.toArray,
      this.force,
      this.altitude,
      this.color.toArray
    );
    //        console.log(this.timestamp)
    return array;
  }
  writeToData(byteBuffer: ByteBuffer) {
    byteBuffer.writeDouble(this.timestamp);
    byteBuffer.writeShort(this.position.x);
    byteBuffer.writeShort(this.position.y);
    byteBuffer.writeFloat(this.azimuth.x);
    byteBuffer.writeFloat(this.azimuth.y);
    byteBuffer.writeFloat(this.force);
    byteBuffer.writeFloat(this.altitude);

    //TODO: Vec writeData
    //dataView.setUint16(dataView.byteOffset,this.position.y)
  }

  static fromData(byteBuffer: ByteBuffer) {
    var timestamp = byteBuffer.readDouble();
    var posx = byteBuffer.readShort();
    var posy = byteBuffer.readShort();
    var azix = byteBuffer.readFloat();
    var aziy = byteBuffer.readFloat();
    var force = byteBuffer.readFloat();
    var altitude = byteBuffer.readFloat();
    var point = new PointData(
      new Vec([posx, posy, 0, 1]),
      new Vec([azix, aziy]),
      force,
      altitude
    );
    point.timestamp = timestamp;
    return point;
  }
  get dataSize() {
    return 32;
  }
}



