export default class BrushShaderManager {
  private pencil_vert = document.getElementById("pencil-vertex");
  private pencil_frag = document.getElementById("pencil-fragment");
  pencilShader = new GL.Shader(
    this.pencil_vert.textContent,
    this.pencil_frag.textContent
  );
  private vertBrush = document.getElementById("vertex-brush");
  private fragBrush = document.getElementById("fragment-brush");
  vertexShader = new GL.Shader(
    this.vertBrush.textContent,
    this.fragBrush.textContent
  );
  cursorCircleShader = new GL.Shader(cursorVertext, cursorFragment);
}
var cursorVertext = `
uniform mat4 mvp;
varying vec2 coord;
void main()
{
  coord = gl_TexCoord.xy;
  gl_Position = mvp * gl_Vertex;
}`;

var cursorFragment = `
varying vec2 coord;
uniform sampler2D tex0;
uniform float border; // 0.01
uniform float size;
//uniform float circle_radius; // 0.5
//uniform vec4 circle_color; // vec4(1.0, 1.0, 1.0, 1.0)
//uniform vec2 circle_center; // vec2(0.5, 0.5)    
void main()
{
  vec4 circle_color = vec4(0.0, 0.0, 0.0, 1.0);
  vec2 circle_center = vec2(0.5, 0.5);
  float circle_radius = 0.5;
  vec2 uv = coord.xy;
  
  // vec4 bkg_color = texture2D(tex0,uv * vec2(1.0, -1.0));
  // Offset uv with the center of the circle.
  uv -= circle_center;
  float b = 1.0/size;
  float dist =  sqrt(dot(uv, uv));
  if ( (dist < (circle_radius))  && (dist > (circle_radius-b))){
  gl_FragColor = circle_color;    
  }
}`;
