import { ToolType } from "../event/UserCommand";
export class PaintBrush {
  name: String;
  toolType: ToolType;
  blendtype: BrushBlendType;
  //color:Color;
  size: number = 1;
  dis: number = 2;
  textureName: string;
  shader: GL.Shader;
  //texture: GL.Texture;
  constructor(
    toolType: ToolType,
    texture?: string,
    shader?: GL.Shader,
    type: BrushBlendType = BrushBlendType.brush
  ) {
    this.toolType = toolType;
    this.blendtype = type;

    this.textureName = texture;
    this.shader = shader;
    //this.setTexture();
  }
  setAttribute(size: number, dis: number) {
    this.size = size;
    this.dis = dis;
  }
  // setTexture() {
  //   this.texture = textureManager.getTexture(this.textureName);
  // }
}

export enum BrushTexture {
  flat_brush,
  edge_circle,
  blur_circle
}
export enum BrushBlendType {
  brush,
  eraser,
  smear,
  blur
}



