import { createMaterialStore, ImageRef } from "../CreateMaterialStore";
import * as React from "react";
import { Input, Button } from "semantic-ui-react";
//上傳參考素材圖片input
export class MaterialUploadInput extends React.Component<any, any> {
  constructor() {
    super();
  }
  _handleImageChange(e) {
    e.preventDefault();
    createMaterialStore.previewUploadImagePath(e.target.files[0]);
  }
  render() {
    return (
      <span className="" style={{ padding: "10px" }}>
        {/* <img src={this.state.imagePreviewUrl} alt="" /> */}
        <Button as="label" for="file">
          選擇檔案
          <Input
            multiple
            fluid
            type="file"
            id="file"
            style={{ display: "none" }}
            onChange={e => {
              this._handleImageChange(e);
            }}
          />
        </Button>
      </span>
    );
  }
}



