import { Button, Icon } from "semantic-ui-react";
import * as React from "react";
import { observer } from "mobx-react";
import { observable } from "mobx";
import Resizable, { Size } from "react-resizable-box";
import Draggable from "react-draggable";
import { materialStore } from "../../store/MaterialStore";
import { paintCanvasStore } from "../../store/PaintCanvasStore";
//const Draggable = require("react-draggable");
@observer
export class RefImageWindow extends React.Component<any, any> {
  constructor(props) {
    super(props);
    this.onImgLoad = this.onImgLoad.bind(this);
  }

  getSize() {
    //ReactDOM.findDOMNode("awer").clientHeight
  }
  @observable imgWidth: any = 400;
  @observable imgHeight: any = 400;

  onImgLoad({ target: img }) {
    var width = img.offsetWidth;
    var height = img.offsetHeight;
    console.log(width);
    console.log(height);
    this.imgWidth = width * this.imgHeight / height;
  }
  render() {
    return (
      //https://github.com/mzabriskie/react-draggable
      <Draggable handle=".handle">
        <div
          className="draggable-element"
          style={{
            zIndex: 1000,
            position: "absolute",
            display: paintCanvasStore.tab ? "block" : "none"
          }}
        >
          <Button
            onClick={() => {
              materialStore.prevImage();
            }}
          >
            <Icon className="left arrow" />
            上一張
          </Button>
          <Button
            onClick={() => {
              materialStore.nextImage();
            }}
          >
            下一張<Icon className="right arrow icon" />
          </Button>
          <div className="">Handle</div>
          <Resizable
            style={{
              borderStyle: "solid",
              borderWidth: "3px",
              borderColor: "red",
              zIndex: 1000
            }}
            lockAspectRatio={true}
            bounds="window"
            onResize={(e, d, r, delta: Size) => {
              this.imgWidth = r.offsetWidth;
              this.imgHeight = r.clientHeight;
            }}
            className="item"
            width={this.imgWidth}
            height={this.imgHeight}
          >
            <img
              className="handle"
              draggable={false}
              onLoad={this.onImgLoad}
              style={{ width: this.imgWidth - 6, zIndex: 99999 }}
              src={
                materialStore.currentMaterial.detail.imgURLs[
                  materialStore.currentMaterial.currentImgIndex
                ]
              }
            />
          </Resizable>
        </div>
      </Draggable>
    );
  }
}

