import { GLContextBuffer } from "./GLContextBuffer";
import { TextureManager } from "../PaintTool/TextureManager";
import PaintStroke from "../stroke/PaintStroke";
import { UserCommand } from "../event/UserCommand";
import { PointData, PaintPoint, PaintPointArray } from "../stroke/PaintPoint";
import { PaintBrush, BrushBlendType } from "../PaintTool/PaintBrush";
import {
  paintToolManager,
  colorPalatte,
  mouseEventHandler,
  textureManager
} from "../painter";
import { Vec } from "../Math/Vector";

export default class GLStrokeRenderer {
  mvp: GL.Matrix;
  brushTexture: GL.Texture;
  canvasTexture: GL.Texture;
  uniforms: { [name: string]: number } = {};
  vertexBuffers: { [name: string]: any } = {};
  constructor(canvasTexture: GL.Texture, mvp: GL.Matrix) {
    this.canvasTexture = canvasTexture;
    this.mvp = mvp;
  }
  bindUniform(uniformName: string, uniformValue: number) {
    this.uniforms[uniformName] = uniformValue;
  }
  bindAttribute(attributeName: string, attributeValue: any) {
    var vertices = new GL.Buffer(gl.ARRAY_BUFFER, Float32Array);
    vertices.data = vertices.data.concat(attributeValue);
    vertices.compile();
    this.vertexBuffers[attributeName] = vertices;
  }
  bindAndDrawPoints(pointData: PaintPointArray, brush: PaintBrush) {
    this.uniforms = {};
    this.vertexBuffers = {};
    //this.bindTextures();
    this.bindUniform("texture", 0);
    this.bindUniform("mvp", this.mvp.m);
    this.bindUniform("brushSize", brush.size);
    this.bindAttribute("vertexPosition", pointData.positions);
    this.bindAttribute("velocity", pointData.velocities);
    this.bindAttribute("force", pointData.forces);
    this.bindAttribute("brushColor", pointData.colors);
    this.bindAttribute("altitude", pointData.altitude);
    this.bindAttribute("azimuth", pointData.azimuth);
    brush.shader.uniforms(this.uniforms);
    brush.shader.drawBuffers(this.vertexBuffers, null, gl.POINTS);
  }

  setBlendFunction(type: BrushBlendType) {
    gl.enable(gl.BLEND);
    if (type == BrushBlendType.eraser) {
      console.log("eraser");
      //eraser
      gl.blendEquation(gl.FUNC_REVERSE_SUBTRACT);
      //gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);
      gl.blendFunc(gl.SRC_ALPHA, gl.ONE);
    } else if (type == BrushBlendType.brush) {
      //pen
      gl.blendEquation(gl.FUNC_ADD);
      gl.blendFunc(gl.ONE, gl.ONE_MINUS_SRC_ALPHA);
    }
  }
  setCanvasTexture(t: GL.Texture) {
    this.canvasTexture = t;
  }
  drawPoint(point: PointData, paintbrush: PaintBrush) {}
  drawPoints(points: PointData[], paintbrush: PaintBrush) {
    var buffer = this.interpolatePoints(points, paintbrush);
    console.log(buffer);
    if (buffer == null || buffer.positions.length == 0) {
      return false;
    }

    //  t0 = performance.now();
    this.renderPoints(buffer, paintbrush, this.canvasTexture, false);
    // t1 = performance.now();
    // console.log("render" + (t1 - t0) + " milliseconds.")
    GLContextBuffer.isDirty = true;
    return true;
  }
  interpolatePoints(points: PointData[], brush: PaintBrush) {
    //TODO:
    var paintPoints = new Array<PaintPoint>();
    points.forEach(point => {
      paintPoints.push(
        new PaintPoint(
          point.position,
          point.force,
          point.altitude,
          point.azimuth,
          //TODO: velocity Vec
          new Vec([0, 0]),
          colorPalatte.palatteColor.vec4
        )
      );
    });
    var buffer = new PaintPointArray();
    var lastPoint = paintPoints[0];
    for (var i = 0; i < points.length; i++) {
      var result = PaintPoint.interpolate(
        lastPoint,
        paintPoints[i],
        buffer,
        brush
      );
      if (result) {
        lastPoint = paintPoints[i];
      }
    }
    if (lastPoint == paintPoints[0]) return null;
    else return buffer;
  }

  //tempLayer:Layer

  renderPoints(
    paintPointArray: PaintPointArray,
    paintbrush: PaintBrush,
    canvas: GL.Texture,
    clear: boolean = false
  ) {
    var brushTexture = textureManager.getTexture(paintbrush.textureName);
    brushTexture.bind(0);
    //console.log(paintPointArray.positions[0]);
    canvas.drawTo(() => {
      if (clear == true) {
        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
        gl.clearColor(0, 0, 0, 0);
      }
      this.setBlendFunction(paintbrush.type);
      this.bindAndDrawPoints(paintPointArray, paintbrush);
      gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);
    });
    brushTexture.unbind(0);
  }
  // drawStrokes(strokes: Array<UserCommand>) {
  //   var pen = new PaintBrush(); //TODO: temp brush

  //   t0 = performance.now();
  //   strokes.forEach(stroke => {
  //     var s = stroke.commandData as PaintStroke;
  //     this.interpolatePoints(s.points, pen);
  //   });
  //   t1 = performance.now();
  //   console.log("calculation" + (t1 - t0) + " milliseconds.");

  //   var t0 = performance.now();
  //   this.renderPoints(buffer, pen, this.canvasTexture);
  //   var t1 = performance.now();

  //   console.log("render" + (t1 - t0) + " milliseconds.");
  // }

  drawCursor(layer: GL.Texture) {
    if (mouseEventHandler.cursorPoint != null) {
      var buffer = new PaintPointArray();
      var brush = paintToolManager.cursor;
      var p = mouseEventHandler.cursorPoint;
      buffer.addPointData(p);
      this.renderPoints(buffer, brush, layer, true);

      //var brushTexture = this.textureManager.getTexture(brush.textureName);
      // console.log(brush);
      // console.log(brush.textureName);
      // console.log(brushTexture);
      // brushTexture.bind(0);
      // console.log(p);
      // var p = mouseEventHandler.lastPoint;
      // buffer.addPointData(p);
      // // this.interpolatePoints([mouseEventHandler.lastPoint], brush, buffer);
      // this.setBlendFunction(brush.type);
      // this.bindAndDrawPoints(buffer, brush);
      // brushTexture.unbind(0);
    }
  }
}



