import {
  paintReplayer,
  fileManager,
  paintToolManager,
  glContextBuffer
} from "../painter";
import PaintStroke from "../stroke/PaintStroke";
import { PointData } from "../stroke/PaintPoint";
import { PaintBrush } from "../PaintTool/PaintBrush";
import {
  UserCommand,
  UserCommandType,
  ToolAttributeType,
  ToolAttributeCommandData
} from "../event/UserCommand";
import { paintCanvasStore } from "../../../components/store/PaintCanvasStore";

export class PaintRecorder {
  constructor() {
    //this.clip = new SAClip(0);
  }
  get artwork() {
    return fileManager.currentFile.artwork;
  }
  get clip() {
    return fileManager.currentFile.artwork.masterClip;
  }
  get commands() {
    return this.clip.fullCommands;
  }
  currentStroke: PaintStroke;
  //TODO:for multiple user
  userStroke: { [uid: string]: PaintStroke } = {};
  mouseDown(p: PointData) {
    /*
        if(paintToolManager.checkDirty())
        {
            //save attribute to command
            let cmd = new UserCommand(UserCommandType.tool_attribute);
            paintToolManager.reset()
        }*/
    //var color = this.paintToolManager.currentBrush.color;
    this.currentStroke = new PaintStroke();
    this.currentStroke.addPoints([p]);
  }
  //Local User move
  mouseMove(points: PointData[]) {
    //this.currentStroke.addPoints(points);
    //send to other
  }
  mouseUp() {
    let cmd = new UserCommand(UserCommandType.stroke);
    cmd.commandData = this.currentStroke;
    this.addCommand(cmd);
  }

  userStartDraw(user: number, p: PointData) {
    this.userStroke[user] = new PaintStroke();
    this.userStroke[user].addPoints([p]);
    // glContextBuffer.strokeRenderer.drawPoint(
    //   p,
    //   paintToolManager.userTool[user]
    // );
  }
  userDrawMove(user: number, points: PointData[]) {
    this.userStroke[user].addPoints(points);
  }
  userDrawDone(user: number) {
    let cmd = new UserCommand(UserCommandType.stroke);
    cmd.commandData = this.userStroke[user];
    this.addCommand(cmd);
  }

  toolAttr(attr: ToolAttributeType, val: number) {
    let cmd = new UserCommand(UserCommandType.tool_attribute);
    cmd.commandData = new ToolAttributeCommandData(attr, val);
    this.addCommand(cmd);
  }
  zoom(pivotx: number, pivoty: number, val: number) {
    let event = new UserCommand(UserCommandType.zoom);
    //TODO:
    //event.commandData = val;
  }
  undoCommands: UserCommand[] = [];
  addCommand(cmd: UserCommand) {
    // if (this.clip.fullCommands.length == 0) {
    //   paintToolManager.usePencil();
    // }
    this.clip.addCommand(cmd);
    this.clip.currentCommandIndex++;
    paintCanvasStore.commandLength = this.clip.fullCommands.length;
    //paintViewController.updateStrokeNum()
  }

  undoCommand(user: number) {
    if (this.clip.fullCommands.length > 0) {
      //var beUndo = this.clip.renderCommands.pop();
      var beUndo;
      do {
        beUndo = this.clip.fullCommands.pop();
      } while (beUndo.type != UserCommandType.stroke);

      //   var undoCmd = new UserCommand(UserCommandType.undo, beUndo);
      //   this.addCommand(undoCmd);
      if (beUndo.type == UserCommandType.stroke)
        this.clip.undoCommands.push(beUndo);
      //TODO: need optimize
      paintReplayer.drawAll();
    }
  }

  redoCommand() {}
}

