export class Vec{
    dimension:number
    constructor(val:number[])
    {        
        this.val = val;
        this.dimension = val.length;
    }
    get x(){
        return this.val[0]
    }
    get y()
    {
        return this.val[1]
    }
    val:number[]
    
    subtract(vec:Vec)
    {
        var t = new Array<number>(4);
        for(var i=0;i<this.dimension;i++)
            t[i] = this.val[i]-vec.val[i];
        
        return new Vec(t);
    }
    add(vec:Vec)
    {
        var t = new Array<number>(4);
        for(var i=0;i<this.dimension;i++)
            t[i] = this.val[i]+vec.val[i];
        
        return new Vec(t);
    }
    multiply(val:number)
    {
        var t = new Array<number>(4);
        for(var i=0;i<this.dimension;i++)
            t[i] = this.val[i]*val;
        
        return new Vec(t);
    }
    dot(vec:Vec) {
        var val = 0;
        for(var i=0;i<this.dimension;i++)
            val+= this.val[i]*vec.val[i];
        
        return val;
    }
    length() {
        return Math.sqrt(this.dot(this));
    }
    static lerp(v1:Vec, v2:Vec, fraction:number) {
        return v2.subtract(v1).multiply(fraction).add(v1);
    }
    get toArray()
    {
        return this.val;
    }
    static parseData(array:number[],ptr:number){
        var vec = new Vec(array.slice(ptr,4));
        return vec;
    }
}


