import { StorageManager } from "./StorageManager";
import { LoginManager } from "./LoginManager";
import { DataModel } from "../../database/DataModel";
import * as firebase from "firebase";
//import * as firebase from "firebase"
import { SAFile } from "../painter/artwork/SAFile";
import SnapShot = firebase.database.DataSnapshot;
//https://developers.facebook.com/docs/facebook-login/manually-build-a-login-flow#token
//https://competenepal.com/lets-make-a-facebook-login-system-in-electron-that-actually-works/

export default class FirebaseManager {
  database: firebase.database.Database;
  storageRef: firebase.storage.Reference;
  loginManager: LoginManager;
  storageManager: StorageManager;
  constructor() {
    var config = {
      apiKey: "AIzaSyCWSE5dZ863e8mTL7dcN6YJfvnxD3NyQ4k",
      authDomain: "sketch-academy-share.firebaseapp.com",
      databaseURL: "https://sketch-academy-share.firebaseio.com",
      projectId: "sketch-academy-share",
      storageBucket: "sketch-academy-share.appspot.com",
      messagingSenderId: "632462584527"
    };
    var app = firebase.initializeApp(config);

    this.loginManager = new LoginManager();
    this.storageManager = new StorageManager();
    this.database = firebase.database();
    this.storageRef = firebase.storage().ref();
    //this.test();
  }

  desktop_fb_signin(
    name: string,
    email: string,
    photo: string,
    access_token: string
  ) {
    // Build Firebase credential with the Facebook access token.
    var credential = firebase.auth.FacebookAuthProvider.credential(
      access_token
    );
    // Sign in with credential from the Google user.
    firebase
      .auth()
      .signInWithCredential(credential)
      .then((currentUser: firebase.User) => {
        currentUser
          .updateProfile({ displayName: name, photoURL: photo })
          .then(
            () => {
              this.loginManager.user = currentUser;
              console.log("update success");
              // Update successful.
            },
            function(error) {
              // An error happened.
              console.log("update error");
            }
          );
        currentUser.updateEmail(email).then(function() {
          console.log("email success");
        });
      })
      .catch(function(error: Error) {
        // Handle Errors here.
        var errorMessage = error.message;
        console.log(errorMessage);
        // The email of the user's account used.

        // The firebase.auth.AuthCredential type that was used.
        //var credential = error.credential;
        // ...
      });
    //
  }

  saveFile(dataB64: String) {
    this.database.ref("file").child("0").set(dataB64);
  }
  //DRAFT:
  getTutorials() {
    this.database.ref("tutorials").once("", function(snapshot: SnapShot, b) {});
  }
  savePracticeFile(practice_id: string, file: SAFile) {
    var blob = new Blob([file.fileString], { type: "application/json" });
    var metadata = {
      contentType: "image/sa"
    };
    var filePath = "pracitces/" + practice_id + "/" + file.__filename;
    var fileRef = this.storageRef.child(filePath);
    fileRef.put(blob, metadata).then(function(snapshot) {
      console.log("save file complete");
    });

    this.database
      .ref()
      .child("practices/" + practice_id)
      .child(file.__filename)
      .set(filePath, function() {
        console.log("save path to db complete");
      });
  }
  getPracticeFile(practice_id: string, callBack: Function) {
    this.database
      .ref()
      .child("practices/" + practice_id)
      .once("value", function(snapshot: SnapShot) {
        console.log(snapshot.val());
        callBack(snapshot.val());
      });
  }

  getDataOnce(
    tableURL: string,
    orderByChild: string = null,
    equalTo: string = null
  ) {
    var query: firebase.database.Query;
    var ref = this.database.ref(tableURL);
    console.log(tableURL);
    console.log(ref);
    console.log(equalTo);

    query = ref;
    if (orderByChild != null) query = query.orderByChild(orderByChild);

    if (equalTo != null) query = query.equalTo(equalTo);

    return query
      .once("value")
      .then((snapShot: SnapShot) => {
        console.log(snapShot.val());

        return snapShot.val();
      })
      .catch(a => {
        console.log(a);
      });
  }

  //DRAFT:
  pushDataModel(url: string, obj: DataModel) {
    return this.database.ref(url).push(obj).then(a => {
      return a;
    });
  }
  setValue(url: string, obj: any) {
    return this.database.ref(url).set(obj);
  }
  updateDataModel(url: string, obj: DataModel) {
    return this.database.ref(url).update(obj);
  }
  getValue(url: string) {
    return this.database.ref(url).once("value").then((snapShot: SnapShot) => {
      return snapShot.val();
    });
  }
  removeValue(url: string) {
    return this.database.ref(url).remove();
  }
}

