import { DataModel, DataObject } from "./DataModel";
import { observable } from "mobx";
export class MaterialObject extends DataObject {
  constructor() {
    super();
    this.brief = new MaterialBriefModel();
    //this.detail = new MaterialDetailModel();
  }
  @observable brief: MaterialBriefModel;
  @observable detail: MaterialDetailModel;
  @observable currentImgIndex: number = 0;

  //@observable likeByUser: boolean;
  //@observable collectByUser: boolean;
}

export class MaterialDetailModel extends DataModel {
  description: string;
  imgURLs: string[];
}

export class MaterialBriefModel extends DataModel {
  created_by: string; //User
  created_at: number; //DateTime?
  title: string;
  like: number = 0;
  pracCount: number = 0;
  coverImgURL: string;
}



