import { IBinaryData } from '../event/UserCommand';
import ByteBuffer from '../../bytebuffer';
import SAClip from './SAClip';
export default class SAArtwork implements IBinaryData{
    
    writeToData(byteBuffer:ByteBuffer) {
        console.log('width:'+this.width)
        console.log('height:'+this.height)
        byteBuffer.writeShort(this.width)
        byteBuffer.writeShort(this.height)
        this.masterClip.writeToData(byteBuffer)
    }
    static fromData(byteBuffer:ByteBuffer) {
        var w = byteBuffer.readShort()
        var h = byteBuffer.readShort()
        console.log('width:'+w)
        console.log('height:'+h)
        var artwork = new SAArtwork(w,h)
        artwork.masterClip = SAClip.fromData(byteBuffer)
        return artwork
    }

    title: string;
    description:string
    width:number;
    height:number;
    masterClip:SAClip;
    reviseClips:Array<SAClip>;    
    constructor(width:number,height:number)
    {
        this.title = "New Artwork"
        this.width = width
        this.height = height
        this.masterClip = new SAClip(0)
    }
    addRevisionClip(at:number)
    {
        this.reviseClips.push(new SAClip(at));
    }
    exportArray(): number[] {
        var array:number[] = [];
        array.push(this.width)
        array.push(this.height)
        var clipBuffer = this.masterClip.exportArray()
        array.push.apply(array,clipBuffer)
        return array
    }
    dataSize()
    {
        return 8+this.masterClip.dataSize()
    }
}


    


