import { SAFile } from "./SAFile";
import ByteBuffer from "../../bytebuffer";
import SAArtwork from "./SAArtwork";
import {
  UserCommand,
  ToolAttributeCommandData,
  UserCommandType,
  ColorCommandData
} from "../event/UserCommand";
import PaintStroke from "../stroke/PaintStroke";
import { PointData } from "../stroke/PaintPoint";
import SAClip from "./SAClip";
export class SAUnArchiver {
  ptr: number = 0;
  buffer: Float64Array;
  parseSAFile(jsonStr: string) {
    var jobj = JSON.parse(jsonStr);
    var artwork = this.parseArtworkFromString(jobj.content);
    var file = new SAFile(jobj.author, artwork);
    file.__path = jobj.path;
    return file;
  }
  parseArtworkFromString(base64String: string) {
    var u8 = this.base64ToBuffer(base64String);
    var dataView = new DataView(u8.buffer);
    var byteBuffer = new ByteBuffer(u8.buffer);

    byteBuffer.front();
    var artwork = SAArtwork.fromData(byteBuffer);

    //var u8 = this.base64ToBuffer(base64String);
    //this.buffer = new Float64Array(u8.buffer);
    //this.ptr = 0
    // var width = this.parseFloat32()
    // var height = this.parseFloat32()
    // var clip = this.parseClip()
    // var artwork = new SAArtwork(width,height)
    // artwork.masterClip = clip
    return artwork;
    //TODO:parse should be out
    //this.masterClip = clip
  }

  /**
     * base64 String to array buffer
     * 
     * @param {string} base64 
     * @returns 
     * @memberof SAArtwork
     */
  base64ToBuffer(base64: string) {
    var binstr = atob(base64);
    var buf = new Uint8Array(binstr.length);
    Array.prototype.forEach.call(binstr, function(ch: string, i: number) {
      buf[i] = ch.charCodeAt(0);
    });
    return buf;
  }
}

class DataPointer {
  current: number;
}



