import { paintRecorder, paintToolManager, glContextBuffer } from "../painter";
import { GLContextBuffer } from "../render/GLContextBuffer";
import { paintCanvasStore } from "../../../components/store/PaintCanvasStore";
import { Key } from "ts-keycode-enum";
import { ToolType } from "../event/UserCommand";
export class KeyBinder {
  constructor() {
    this.initKeyBoardBinding();
  }
  keyDown(event: KeyboardEvent) {
    console.log(event.keyCode);
    var key = true;

    switch (event.keyCode) {
      case Key.B:
        paintToolManager.setMyTool(ToolType.pencil);
        break;
      case Key.E:
        paintToolManager.setMyTool(ToolType.eraser);
        break;
      case Key.R:
        glContextBuffer.clearCanvas();
        if (event.metaKey) {
          key = false;
        }
        break;
      case Key.Tab:
        //tab
        paintCanvasStore.tab = !paintCanvasStore.tab;
        break;
      case Key.P:
        console.log(paintRecorder.clip.fullCommands);
        break;
      //left arrow
      case 37:
        glContextBuffer.canvasPosX -= 0.1;
        GLContextBuffer.isDirty = true;
        break;
      case 39:
        //right arrow
        glContextBuffer.canvasPosX += 0.1;
        GLContextBuffer.isDirty = true;
        break;
      case 38:
        //up arrow
        glContextBuffer.canvasPosY += 0.1;
        GLContextBuffer.isDirty = true;
        break;
      case 40:
        //down arrow
        glContextBuffer.canvasPosY -= 0.1;
        GLContextBuffer.isDirty = true;
        break;
      case 187:
        glContextBuffer.zoom(1.1, 0, 0);
        //glContextBuffer.newScale *= 1.1;
        //glContextBuffer.newScale *= 1.1;
        GLContextBuffer.isDirty = true;
        break;
      case 189:
        glContextBuffer.zoom(1 / 1.1, 0, 0);
        //glContextBuffer.newScale /= 1.1;
        GLContextBuffer.isDirty = true;
        break;
      case 90:
        if (event.metaKey || event.ctrlKey) {
          paintRecorder.undoCommand();
        }
        break;
      default:
        key = false;
        break;
    }

    if (key == true) {
      event.preventDefault();
      event.stopPropagation();
    }
  }

  bindPainterShortCut() {
    document.addEventListener("keydown", this.keyDown);
  }
  initKeyBoardBinding() {
    this.bindPainterShortCut();
  }
  disableShortCut() {
    document.removeEventListener("keydown", this.keyDown);
  }
}



