import * as firebase from "firebase";
import { observable } from "mobx";
import { userStore } from "../../components/store/UserStore";
import { UserObject } from "../../database/UserModel";
export class LoginManager {
  constructor() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        // User is signed in.
        console.log(user);
        this.user = user;

        userStore.currentUser = new UserObject(this.user.uid);
      } else {
        // No user is signed in.
        console.log("no user");
      }
    });
  }
  @observable user: firebase.User;
  fbLogin() {
    var provider = new firebase.auth.FacebookAuthProvider();
    provider.setCustomParameters({
      display: "popup"
    });
    return this.login(provider);
  }
  googleLogin() {
    var provider = new firebase.auth.GoogleAuthProvider();
    provider.setCustomParameters({
      display: "popup"
    });
    return this.login(provider);
  }
  anoymousLogin() {
    return firebase.auth().signInAnonymously().then(result => {
      this.user = result.user;
    });
  }
  private login(provider: firebase.auth.AuthProvider) {
    return firebase.auth().signInWithPopup(provider).then(result => {
      var token = result.credential.accessToken;
      // The signed-in user info.
      this.user = result.user;
      console.log(this.user);
    });
  }
  logout() {
    return firebase.auth().signOut().then(() => {
      window.location.reload();
    });
  }
}



