import { PreviewMaterialGrid } from "./UploadImage/PreviewMaterialGrid";
import * as React from "react";
import { createMaterialStore } from "./CreateMaterialStore";
import { Button } from "semantic-ui-react";
import { Link } from "react-router-dom";
import { DatabaseTableURL } from "../../../engine/Firebase/DatabaseTableURL";
export class PublishMaterialForm extends React.Component<any, any> {
  componentWillMount() {}
  render() {
    return (
      <div>
        <h3>創立完成！</h3>
        <p>
          {createMaterialStore.material.brief.title}
        </p>
        <Link
          to={
            DatabaseTableURL.Material + "/" + createMaterialStore.material.key
          }
        >
          <Button
            onClick={() => {
              createMaterialStore.closeModal();
              location.assign(
                "/#/" +
                  DatabaseTableURL.Material +
                  "/" +
                  createMaterialStore.material.key
              );
            }}
          >
            前往素材頁面
          </Button>
        </Link>
        <Button
          onClick={() => {
            createMaterialStore.closeModal();
          }}
        >
          關閉
        </Button>
      </div>
    );
  }
}



