import { materialStore } from "./../components/store/MaterialStore";
import { MaterialObject } from "./MaterialModel";
import { DataModel, DataObject } from "./DataModel";
import { DatabaseTableURL } from "../engine/Firebase/DatabaseTableURL";
import { firebaseManager } from "../components/index";
import SnapShot = firebase.database.DataSnapshot;
export class UserBriefModel extends DataModel {}
export class UserDetialModel extends DataModel {}

export class UserObject extends DataObject {
  constructor(uid: string) {
    super();
    this.uid = uid;
    this.brief = new UserBriefModel();
  }
  uid: string;
  brief: UserBriefModel;
  detail: UserDetialModel;

  //該使用者建立的素材集
  materialCreated: MaterialObject[] = [];

  //該使用者收藏的素材集
  collectedMaterialKey: string[] = [];
}



