  protected receiveBriefModelArrayHandler(val: any) {
    var array = Array<DataObject>();
    for (var key in val) {
      console.log(val[key]);
      var obj = new DataObject();
      obj.key = key;
      obj.brief = val[key];
      array.push(obj);
    }
    console.log(array);
    return array;
  }

