  //NOT-TESTED
  //bidirectional referenced table
  protected createJoinTableEntry(props: JoinTableProps) {
    firebaseManager
      .setValue(props.url1, true)
      .then(a => {
        console.log(a);
      })
      .catch(a => {
        console.log(a);
      });

    firebaseManager
      .setValue(props.url2, true)
      .then(a => {
        console.log(a);
      })
      .catch(a => {
        console.log(a);
      });
  }
  protected getJoinTableEntry(url: string, obj: DataObject, prop: string) {
    firebaseManager.getValue(url).then((val: any) => {
      obj[prop] = val;
    });
  }

  //NOT-TESTED
  protected removeJoinTableEntry(props: JoinTableProps) {
    firebaseManager.removeValue(props.url1);
    firebaseManager.removeValue(props.url2);
  }

  /**
   * 更新物件資訊(編輯後儲存) 
   * brief & detail both
   * @param 
   */

  protected updateObject(url: string, obj: DataObject) {
    //TODO: url need improve?
    firebaseManager.updateDataModel(url + "/brief/" + obj.key, obj.brief);
    firebaseManager.updateDataModel(url + "/detail/" + obj.key, obj.detail);
  }
