import * as React from "react";
import { Link, match, RouteComponentProps } from "react-router-dom";
import { Tab, Card, Container, Icon, Image } from "semantic-ui-react";
//import { CSSGrid, layout } from "react-stonecutter";
import StackGrid from "react-stack-grid";
import { MaterialCell } from "../Material/View/Menu/MaterialCell";
import { MaterialGrid } from "../Material/View/MaterialGrid";
import { MainContainer } from "../index";
import { SketchGrid } from "../Sketch/SketchGrid";
import { MaterialFilter, MaterialOrder } from "../store/MaterialStore";
import { userStore } from "../store/UserStore";
import { observer } from "mobx-react";

const panes = [
  {
    menuItem: "我的創建",
    render: () =>
      <Tab.Pane>
        <MaterialGrid
          filter={MaterialFilter.mycreation}
          order={MaterialOrder.created_at}
        />
      </Tab.Pane>
  },
  {
    menuItem: "我的收藏",
    render: () =>
      <Tab.Pane>
        <MaterialGrid
          filter={MaterialFilter.mycollection}
          order={MaterialOrder.created_at}
        />
      </Tab.Pane>
  }
];

@observer
export class DashBoardView extends React.Component<any, object> {
  componentWillMount() {
    window.scrollTo(0, 0);
  }

  loadPractice(practices_file_paths) {
    console.log(practices_file_paths);
  }

  startPractice() {
    console.log("start practice");
  }
  render() {
    return (
      <MainContainer>
        <Container>
          {userStore.currentUser &&
            <Tab menu={{ secondary: true, pointing: true }} panes={panes} />}
        </Container>
      </MainContainer>
    );
  }
}



