import { MaterialCell } from "./Menu/MaterialCell";
import {
  MaterialFilter,
  MaterialOrder,
  materialStore
} from "../../store/MaterialStore";
import StackGrid from "react-stack-grid";
import * as React from "react";
import { observer } from "mobx-react";

var isTesting = false;
@observer
export class MaterialGrid extends React.Component<
  {
    filter: MaterialFilter;
    order: MaterialOrder;
  },
  { order: MaterialOrder }
> {
  componentWillMount() {
    this.setState({ order: this.props.order });
    //TODO: get filter or ordered data from server
    materialStore.getMaterialForGrid(this.props.filter, this.props.order);
  }

  render() {
    return (
      <StackGrid
        duration={0}
        gutterWidth={20}
        gutterHeight={20}
        monitorImagesLoaded={true}
      >
        {isTesting
          ? materialStore.generateFakeMaterial(10).map(material => {
              return (
                <div>
                  {console.log("??")}
                  <MaterialCell data={material} />
                </div>
              );
            })
          : materialStore.materials.map(material => {
              return (
                <div>
                  <MaterialCell data={material} />
                </div>
              );
            })}
      </StackGrid>
    );
  }
}

