export class TextureManager {
  constructor() {
    //this.textureURLDict["flat"] = GL.Texture.fromURL("img/brush/flat.png",{format:gl.RGBA});
    //var practiceTexture = GL.Texture.fromURL('img/practice/boldhead.jpg',{format:gl.RGBA});
    var bgTexture = GL.Texture.fromURL("./img/paper_sketch.png");
    this.createBrushTextures();
  }
  createBrushTextures() {
    this.textureURLDict["flat"] = "./img/brush/flat_.png";
    this.textureURLDict["circle"] = "./img/brush/circle.png";
    this.textureURLDict["small"] = "./img/brush/small.png";
    this.textureURLDict["pencil"] = "./img/brush/pencil.png";

    this.createTexture("circle", "./img/brush/circle.png");
    this.createTexture("pencil", "./img/brush/pencil.png");
  }

  textureDict: { [id: string]: GL.Texture } = {};
  textureURLDict: { [id: string]: string } = {};
  getTexture(name: string) {
    //var texture = GL.Texture.fromURL(this.textureURLDict[name],{format:gl.RGBA});
    //return texture;
    return this.textureDict[name];
  }
  createTexture(name: string, path: string) {
    var img = new Image();
    img.src = path;
    var textureDict = this.textureDict;
    img.onload = function() {
      var t = GL.Texture.fromImage(img);
      textureDict[name] = t;
      console.log("texture loaded:" + name);
    };
  }
}



