import { createMaterialStore } from "../CreateMaterialStore";
import * as React from "react";
import { MaterialURLInput } from "./MaterialURLInput";
import { Tab } from "semantic-ui-react";
import { MaterialUploadInput } from "./MaterialUploadInput";
export class UploadOrLink extends React.Component<any, any> {
  render() {
    return (
      <Tab
        defaultActiveIndex={createMaterialStore.tabIndex}
        menu={{ secondary: true, pointing: true }}
        panes={[
          {
            menuItem: "連結",
            render: () =>
              <Tab.Pane>
                <MaterialURLInput />
              </Tab.Pane>
          },
          {
            menuItem: "上傳",
            render: () =>
              <Tab.Pane>
                <MaterialUploadInput />
              </Tab.Pane>
          }
        ]}
      />
    );
  }
}



