import { IBinaryData } from "./UserCommand";
import ByteBuffer from "../../bytebuffer";
import PaintStroke from "../stroke/PaintStroke";
import { Color } from "../PaintTool/ColorPalatte";

export class UserCommand implements IBinaryData {
  dataSize(): number {
    //type, timestamp, commandData,
    //undoIndex?
    return 1 + 8 + this.commandData.dataSize();
  }
  constructor(
    type: UserCommandType,
    data?: IBinaryData,
    user: number = 0,
    timestamp: number = Date.now()
  ) {
    this.type = type;
    this.user = user;
    this.commandData = data;
    this.timestamp = timestamp;
  }
  type: UserCommandType;
  timestamp: number;
  commandData: IBinaryData;

  //For multi-user
  user: number;
  undoIndex: number = 0;

  //type
  //timestamp
  //commandData

  // exportArray() {
  //   var array = Array<number>().concat(
  //     this.type,
  //     this.timestamp,
  //     this.userOrder,
  //     this.commandData.exportArray()
  //   );
  //   return array;
  // }
  writeToData(byteBuffer: ByteBuffer) {
    byteBuffer.writeUnsignedByte(this.type);
    byteBuffer.writeDouble(this.timestamp);
    byteBuffer.writeUnsignedByte(this.user);
    this.commandData.writeToData(byteBuffer);
  }
  static fromData(byteBuffer: ByteBuffer) {
    var type = byteBuffer.readUnsignedByte();
    var time = byteBuffer.readDouble();
    var user = byteBuffer.readUnsignedByte();
    var data: IBinaryData;
    switch (type as UserCommandType) {
      case UserCommandType.stroke:
        data = PaintStroke.fromData(byteBuffer);
        break;
      case UserCommandType.color:
        data = ColorCommandData.fromData(byteBuffer);
        break;
      case UserCommandType.tool:
        data = byteBuffer.readUnsignedByte();
        break;
      case UserCommandType.tool_attribute:
        data = ToolAttributeCommandData.fromData(byteBuffer);
        break;
    }
    var command = new UserCommand(type, data, user);
    return command;
  }
}
export interface IBinaryData {
  //exportArray(): number[];
  dataSize(): number;
  writeToData(byteBuffer: ByteBuffer);
}
//undo redo...
export enum UserCommandType {
  stroke = 1,
  zoom = 2,
  tool_attribute = 3,
  layer = 4,
  color = 5,
  tool = 6,
  undo = 7,
  redo = 8
}
export enum ToolType {
  pencil = 0,
  eraser = 1,
  pointPen = 2,
  blur = 3,
  cursor = 100
}
export class ToolCommandData implements IBinaryData {
  constructor(type: ToolType) {
    this.type = type;
  }
  type: ToolType;
  exportArray(): number[] {
    return [this.type];
  }
  dataSize(): number {
    return 1;
  }
  writeToData(byteBuffer: ByteBuffer) {
    byteBuffer.writeUnsignedByte(this.type);
  }
}
export enum LayerCommandType {
  add,
  delete,
  move
}
export class LayerCommandData implements IBinaryData {
  dataSize(): number {
    throw new Error("Method not implemented.");
  }
  writeToData(byteBuffer: ByteBuffer) {
    throw new Error("Method not implemented.");
  }
  static fromData(byteBuffer: ByteBuffer) {
    byteBuffer.readUnsignedByte();
  }
  type: LayerCommandType;
  index: number;
  to: number;
  fromArray(nums: number[]) {
    this.type = nums[0];
    this.index = nums[1];
    this.to = nums[2];
  }
  exportArray() {
    return [this.type, this.index, this.to];
  }
  // move layer form "index" to "to"
}

export class ToolAttributeCommandData implements IBinaryData {
  dataSize(): number {
    throw new Error("Method not implemented.");
  }
  writeToData(byteBuffer: ByteBuffer) {
    throw new Error("Method not implemented.");
  }
  constructor(attr?: ToolAttributeType, val?: number) {
    this.attribute = attr;
    this.val = val;
  }
  attribute: ToolAttributeType;
  val: number;
  isDirty: boolean = false;
  fromArray(nums: number[]) {
    this.attribute = nums[0];
    this.val = nums[1];
  }
  static fromData(byteBuffer: ByteBuffer) {
    var attribute = byteBuffer.readUnsignedByte();
    var val = byteBuffer.readInt();
    return new ToolAttributeCommandData(attribute, val);
  }
  static size: number = 2;
  exportArray() {
    return [this.attribute, this.val];
  }
}
export class ColorCommandData implements IBinaryData {
  dataSize(): number {
    return 4;
  }
  writeToData(byteBuffer: ByteBuffer) {
    throw new Error("Method not implemented.");
  }
  constructor(color?: Color) {
    this.val = color.val;
  }
  exportArray(): number[] {
    return this.val;
  }
  val: number[];
  fromArray(num: number[]) {
    //len must be 3
    if (num.length != 3) {
      throw new Error("color array length");
    }
    this.val = num;
  }
  static fromData(byteBuffer: ByteBuffer) {
    var r = byteBuffer.readUnsignedByte();
    var g = byteBuffer.readUnsignedByte();
    var b = byteBuffer.readUnsignedByte();
    var a = byteBuffer.readUnsignedByte();
    var color = new Color(r, g, b, a);
    return new ColorCommandData(color);
  }
}
export enum ToolAttributeType {
  size = 1,
  opacity = 2,
  mix = 3
}



