import SAArtwork from './SAArtwork';
import { SAArchiver } from './SAArchiver';
export class SAFile{
    __path:string
    __filename:string = "未命名"
    author:string;
    artwork:SAArtwork
    constructor(author:string,artwork:SAArtwork)
    {
        this.author = author
        this.artwork = artwork
    }
    get fileString()
    {
        return SAArchiver.serialize(this)
    }
}


